import React from 'react';
import Dropzone from 'react-dropzone';
import {makeStyles} from '@material-ui/core/styles';
import I18n from '@iobroker/adapter-react/i18n';
import Button from '@material-ui/core/Button';

const style = makeStyles(theme => ({
    baseStyle: {
        flex: 1,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: '20px',
        borderWidth: 2,
        borderRadius: 2,
        borderColor: '#eeeeee',
        borderStyle: 'dashed',
        backgroundColor: '#fafafa',
        color: '#bdbdbd',
        outline: 'none',
        transition: 'border .24s ease-in-out',
        width: '100%'
    }
}));

/*const activeStyle = {
    borderColor: '#2196f3'
};

const acceptStyle = {
    borderColor: '#00e676'
};

const rejectStyle = {
    borderColor: '#ff1744'
};*/


function StyledDropZone (props){

    const classes = style();

    const onDropFile = (res) => {
        props.onDrop(res);
    }

    return (
        <Dropzone onDrop={onDropFile}
                  accept=".knxproj"
                  multiple={false}
        >
            {({getRootProps, getInputProps}) => (
                <section className="container">
                    <div {...getRootProps({className: "container"})}>
                        <input {...getInputProps()} />
                        <Button className={classes.baseStyle}>
                            {I18n.t('Drag \'n\' drop knxproj file here, or click to select file')}
                        </Button>
                    </div>
                </section>
            )}
        </Dropzone>
    );
}

export {StyledDropZone};
