import React from 'react';
import {withStyles} from '@material-ui/core/styles';
import { MuiThemeProvider } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import GenericApp from '@iobroker/adapter-react/GenericApp';
import Loader from '@iobroker/adapter-react/Components/Loader';

import I18n from '@iobroker/adapter-react/i18n';
import TabConfiguration from './Tabs/Configuration'
import TabGaTools from './Tabs/GaTools';

//import theme from "@iobroker/adapter-react/Theme";

const styles = theme => ({
    root: {},
    tabContent: {
        padding: 10,
        height: 'calc(100% - 64px - 48px - 20px)',
        overflow: 'auto'
    },
    tabContentIFrame: {
        padding: 10,
        height: 'calc(100% - 64px - 48px - 20px - 38px)',
        overflow: 'auto'
    }
});

class App extends GenericApp {
    constructor(props) {
        const extendedProps = {...props};
        extendedProps.encryptedFields = ['pass'];
        extendedProps.translations = {
            'en': require('./i18n/en'),
            'de': require('./i18n/de'),
            'ru': require('./i18n/ru'),
            'pt': require('./i18n/pt'),
            'nl': require('./i18n/nl'),
            'fr': require('./i18n/fr'),
            'it': require('./i18n/it'),
            'es': require('./i18n/es'),
            'pl': require('./i18n/pl'),
            'zh-cn': require('./i18n/zh-cn')
        };
        super(props, extendedProps);

        this.updateAliveState = this.updateAliveState.bind(this);
    }

    componentDidMount() {
        this.setState({
            licenseValid : false,
            selectedTab: 'configuration',
            socketConnected : false
        });
    }

    componentWillUnmount() {
        this.socket.unsubscribeState(`system.adapter.${this.adapterName}.${this.instance}.alive`, this.updateAliveState);
    }

    updateAliveState(id, newState){
        // Changing state
        if (newState && newState.hasOwnProperty('val') && id.includes('alive')) {
            this.setState({
                instanceAlive: newState.val,
                selectedTab : newState.val ? this.state.selectedTab : 'configuration'
            });
            //this.selectTab('configuration', 0);
        }
    }

    onConnectionReady() {
        // requests available if socket connection persists
        this.setState({
            socketConnected : true
        });

        this.socket.getObject('system.meta.uuid')
            .then(res => {
                this.setState({hostUUID : res && res.native && res.native.uuid && res.native.uuid.length > 0 ? res.native.uuid : ''});
            })
            .catch(err => {
                this.setState({hostUUID : ''});
            });

        this.socket.getState(`system.adapter.${this.adapterName}.${this.instance}.alive`)
            .then(res => {
                this.state.instanceAlive = res;
                return res;
            })
            .catch(err =>{
                return false;
            });

        this.socket.subscribeState(`system.adapter.${this.adapterName}.${this.instance}.alive`, true, this.updateAliveState);
    }

    updateNativeValue(attr, value, cb) {
        if (attr === 'validLicense'){
            this.setState({licenseValid : value});
        } else {
            super.updateNativeValue(attr, value, cb);
        }
    }

    getSelectedTab() {
        const tab = this.state.selectedTab;
        if (!tab || tab === 'configuration') {
            return 0;
        } else
        if (tab === 'gatools') {
            return 1;
        }
    }

    render() {

        if (!this.state.loaded || !this.state.socketConnected ) {
            return <MuiThemeProvider theme={this.state.theme}>
                <Loader theme={this.state.themeType}  />
            </MuiThemeProvider>;
        }

        return <MuiThemeProvider theme={this.state.theme}>
            <div className="App" style={{background: this.state.theme.palette.background.default, color: this.state.theme.palette.text.primary}}>
                <AppBar position="static">
                    <Tabs value={this.getSelectedTab()} onChange={(e, index) => this.selectTab(e.target.parentNode.dataset.name, index)} scrollButtons="auto">
                        <Tab label={I18n.t('Configuration')} data-name="configuration" />
                        {this.state.licenseValid && this.state.instanceAlive  &&
                            <Tab label={I18n.t('GA-Tools')} data-name="gatools" />
                        }
                    </Tabs>
                </AppBar>

                <div className={this.isIFrame ? this.props.classes.tabContentIFrame : this.props.classes.tabContent} style={{width: '100%'}}>
                    {(this.state.selectedTab === 'configuration' || !this.state.selectedTab) &&
                        <TabConfiguration
                            key="configuration"
                            common={this.common}
                            socket={this.socket}
                            native={this.state.native}
                            onError={text => this.setState({errorText: (text || text === 0) && typeof text !== 'string' ? text.toString() : text})}
                            onLoad={native => this.onLoadConfig(native)}
                            instance={this.instance}
                            adapterName={this.adapterName}
                            instanceAlive={this.state.instanceAlive}
                            changed={this.state.changed}
                            theme={this.state.theme}
                            hostUUID={this.state.hostUUID}
                            onChange={(attr, value, cb) => this.updateNativeValue(attr, value, cb)}
                        />
                    }
                    {this.state.selectedTab === 'gatools' && this.state.licenseValid && this.state.instanceAlive &&
                        <TabGaTools
                            key="gatools"
                            common={this.common}
                            socket={this.socket}
                            native={this.state.native}
                            onError={text => this.setState({errorText: (text || text === 0) && typeof text !== 'string' ? text.toString() : text})}
                            instance={this.instance}
                            instanceAlive={this.state.instanceAlive}
                            adapterName={this.adapterName}
                            onShowError={error => this.showError(error)}
                        />
                    }
                </div>
                {this.renderError()}
                {this.renderSaveCloseButtons()}
            </div>
        </MuiThemeProvider>;
    }
}

export default withStyles(styles)(App);
