/*
    GaProps is part of
        - GaTools
*/

import React, {useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import I18n from '@iobroker/adapter-react/i18n';
import {
    Card,
    CardContent,
    Checkbox,
    FormControl,
    InputLabel,
    List,
    ListItem,
    ListItemIcon,
    ListItemText,
    MenuItem,
    Select
} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";

const getDPTfromDPTObject = (dpt, dptObj) => {
    if (dpt !== undefined && dpt.length) {
        let dpTemp = dpt.replace('DPT', '');
        if (dptObj[dpTemp]) {
            return [dptObj[dpTemp]];
        } else {
            return [{id: dpt}];
        }
    } else {
        return [{id: ''}];
    }
}

function GaProps(props) {

    const {gaObjArr, dptObjs} = props;

    const trwuChecked = {
        write: false,
        read: false,
        update: false
    }

    const [dptObj, setDPTObj] = useState({});
    const [rwuChecked, setrwuChecked] = useState(trwuChecked);
    const [selectedDPT, setSelectedDPT] = useState('');
    const [gaObjArray, setGaObjArray] = useState(gaObjArr);
    const [gaObj, setGaObj] = useState({});

    useEffect(() => {
        switch (gaObjArr.length) {
            case 0 :
                setGaObj({_id: false});
                setrwuChecked({
                    write: false,
                    read: false,
                    update: false
                });
                break;
            case 1:
                setGaObj(gaObjArr[0]);
                if (gaObjArr[0] && gaObjArr[0].native && gaObjArr[0].native.hasOwnProperty('dpt')) {
                    setSelectedDPT(gaObjArr[0].native.dpt.replace('DPT', ''));
                }
                setChecked({
                    read: gaObjArr[0] && gaObjArr[0].common && gaObjArr[0].common.read ? gaObjArr[0].common.read : false,
                    write: gaObjArr[0] && gaObjArr[0].common && gaObjArr[0].common.write ? gaObjArr[0].common.write : false
                });
                break;
            default : {
                setChecked({
                    read: false,
                    write: false
                });
                setSelectedDPT('');
                setGaObjArray(gaObjArr);
            }

        }
    }, [gaObjArr]);

    useEffect(() => {
        setDPTObj(dptObjs);
    }, [dptObjs]);

    const handlePropsChange = e => {
        let tdpt = getDPTfromDPTObject('DPT' + e.target.value, dptObj);
        if (gaObjArray.length === 0 || gaObj._id !== false) {
            if (gaObj && gaObj._id && gaObj._id !== false) {
                gaObj.native['dpt'] = 'DPT' + e.target.value;
                gaObj.common['unit'] = tdpt[0].unit;
                gaObj.common['type'] = tdpt[0].type;
                setSelectedDPT(e.target.value);
                props.onClick({id: 'gaProps', gaObj: gaObj});
            }
        } else if (gaObjArray.length > 0) {
            for (let cnt = 0; cnt < gaObjArray.length; cnt++) {
                gaObjArray[cnt].native['dpt'] = 'DPT' + e.target.value;
                gaObjArray[cnt].common['unit'] = tdpt[0].unit;
                gaObjArray[cnt].common['type'] = tdpt[0].type;
            }
            setSelectedDPT(e.target.value);
            props.onClick({id: 'gaProps', gaObjArr: gaObjArray});
        }
    }

    const setChecked = (obj) => {
        if (obj && obj.hasOwnProperty('write') && obj.hasOwnProperty('read')) {
            let trwu = trwuChecked;

            //  w   r   =>  !w  r   u
            //  w   !r  =>  w   !r  !u
            //  !w  r   =>  !w  r   !u
            //  !w  !r  =>  !w  !r  !u

            if (obj.write && obj.read) {
                trwu.write = false;
                trwu.read = true;
                trwu.update = true;
            }

            if (obj.write && !obj.read) {
                trwu.write = true;
                trwu.read = false;
                trwu.update = false;
            }

            if (!obj.write && obj.read) {
                trwu.write = false;
                trwu.read = true;
                trwu.update = false;
            }

            if (!obj.write && !obj.read) {
                trwu.write = false;
                trwu.read = false;
                trwu.update = false;
            }
            setrwuChecked(trwu);
        }
    }

    const handlePropsClick = e => {
        if (gaObjArray.length === 0 || gaObj._id !== false) {
            if (gaObj && gaObj._id && gaObj._id !== false) {
                let rwuPropTemp = {
                    write: gaObj.common.write || false,
                    read: gaObj.common.read || true
                };
                switch (e.target.id) {
                    case 'write' :
                        rwuPropTemp.write = !gaObj.common.write || false;
                        rwuPropTemp.read = !rwuPropTemp.write;
                        break;
                    case 'read' :
                        rwuPropTemp.read = !gaObj.common.read || true;
                        rwuPropTemp.write = !rwuPropTemp.read;
                        break;
                    case 'update' :
                        if (gaObj.common.write && gaObj.common.read) {
                            rwuPropTemp.write = false;
                        } else if (!gaObj.common.write && gaObj.common.read) {
                            rwuPropTemp.write = true;
                        } else if (!gaObj.common.write && !gaObj.common.read) {
                            rwuPropTemp.write = true;
                        }
                        rwuPropTemp.read = true;
                        break;
                    default:
                        rwuPropTemp.write = false;
                        rwuPropTemp.read = false;
                }
                gaObj.common['read'] = rwuPropTemp.read;
                gaObj.common['write'] = rwuPropTemp.write;
                setChecked(rwuPropTemp);
                props.onClick({id: 'gaProps', gaObj: gaObj});
            }
        } else {
            let rwuPropTemp = {write: false, read: true};
            let updateChecked = false;
            let readChecked = false;
            let writeChecked = false;

            switch (e.target.id) {
                case 'write' :
                    rwuPropTemp.write = !rwuChecked.write;
                    rwuPropTemp.read = false;
                    readChecked = false;
                    writeChecked = !rwuChecked.write;
                    updateChecked = false;
                    break;
                case 'read' :
                    rwuPropTemp.write = false;
                    rwuPropTemp.read = !rwuChecked.read;
                    readChecked = !rwuChecked.read;
                    writeChecked = false;
                    updateChecked = rwuChecked.update;
                    break;
                case 'update' :
                    if (rwuChecked.write && rwuChecked.read) {
                        rwuPropTemp.write = false;
                    } else if (!rwuChecked.write && rwuChecked.read) {
                        rwuPropTemp.write = true;
                    } else if (!rwuChecked.write && !rwuChecked.read) {
                        rwuPropTemp.write = true;
                    }
                    writeChecked = false;
                    readChecked = rwuChecked.read;
                    updateChecked = !rwuChecked.update;
                    rwuPropTemp.read = true;
                    break;
                default:
                    rwuPropTemp.write = false;
                    rwuPropTemp.read = false;
            }

            setrwuChecked({read: readChecked, write: writeChecked, update: updateChecked});
            let tgaObjArray = gaObjArray;
            for (let cnt = 0; cnt < gaObjArray.length; cnt++) {
                tgaObjArray[cnt].common['write'] = rwuPropTemp.write;
                tgaObjArray[cnt].common['read'] = rwuPropTemp.read;
            }
            setGaObjArray(tgaObjArray);
            props.onClick({id: 'gaProps', gaObjArr: gaObjArray});
        }
    }

    return (
        <Card sx={{minHeight: '100%'}}>
            <CardContent>
                <Typography sx={{fontSize: 14}} color="text.secondary" gutterBottom>
                    {I18n.t('GA properties')}
                </Typography>
                <Typography variant="h5" component="div">
                    {gaObjArr.length > 1 ? I18n.t('muliselection') : gaObj && gaObj._id && gaObj._id !== false ? gaObj.common.name : I18n.t('none selected')}
                </Typography>
                <List>
                    <ListItem key="writeBox">
                        <ListItemIcon>
                            <Checkbox
                                id="write"
                                edge="start"
                                checked={gaObjArray && gaObjArray.length > 0 ? rwuChecked.write : gaObj && gaObj._id && gaObj._id !== false ? rwuChecked.write : false}
                                tabIndex={-1}
                                disableRipple
                                inputProps={{'aria-labelledby': 'write'}}
                                onClick={e => handlePropsClick(e)}
                                disabled={gaObjArray && gaObjArray.length > 0 ? false : !(gaObj && gaObj._id && gaObj._id !== false)}
                            />
                        </ListItemIcon>
                        <ListItemText id={'write'} secondary={I18n.t('write')}/>
                    </ListItem>
                    <ListItem key="readBox">
                        <ListItemIcon>
                            <Checkbox
                                id='read'
                                edge="start"
                                checked={gaObjArray && gaObjArray.length > 0 ? rwuChecked.read : gaObj && gaObj._id && gaObj._id !== false ? rwuChecked.read : false}
                                tabIndex={-1}
                                disableRipple
                                inputProps={{'aria-labelledby': 'read'}}
                                onClick={e => handlePropsClick(e)}
                                disabled={gaObjArray && gaObjArray.length > 0 ? false : !(gaObj && gaObj._id && gaObj._id !== false)}
                            />
                        </ListItemIcon>
                        <ListItemText id={'read'} secondary={I18n.t('read')}/>
                    </ListItem>
                    <ListItem key="updateBox">
                        <ListItemIcon>
                            <Checkbox
                                id='update'
                                edge="start"
                                checked={gaObjArray && gaObjArray.length > 0 ? rwuChecked.update : gaObj && gaObj._id && gaObj._id !== false ? rwuChecked.update : false}
                                tabIndex={-1}
                                disableRipple
                                inputProps={{'aria-labelledby': 'update'}}
                                onClick={e => handlePropsClick(e)}
                                disabled={gaObjArray && gaObjArray.length > 0 ? false : !(gaObj && gaObj._id && gaObj._id !== false)}
                            />
                        </ListItemIcon>
                        <ListItemText id={'update'} secondary={I18n.t('update')}/>
                    </ListItem>
                    <ListItem key="formControl">
                        <FormControl fullWidth variant="standard"
                                     disabled={gaObjArray && gaObjArray.length > 0 ? false : !(gaObj && gaObj._id && gaObj._id !== false)}>
                            <InputLabel id={'dptlabel'} variant="standard" htmlFor="uncontrolled-native">
                                DPT
                            </InputLabel>
                            {
                                <Select
                                    value={gaObjArray && gaObjArray.length > 0 ? selectedDPT : gaObj && gaObj._id && gaObj._id !== false ? selectedDPT : ''}
                                    onChange={e => handlePropsChange(e)}
                                >
                                    {dptObj && Object.keys(dptObj).map((dpt) =>
                                        <MenuItem id={dpt} value={dpt} key={dpt}>
                                            {dpt}
                                            {dptObj[dpt].DPT_Name ?
                                                ' - ' + dptObj[dpt].DPT_Name.replace('DPT_', ``).replace(/_/g, ` `) : ''
                                            }
                                            {dptObj[dpt].unit && dptObj[dpt].unit !== 'null' ?
                                                ' - ' + dptObj[dpt].unit : ''
                                            }
                                        </MenuItem>
                                    )}
                                </Select>
                            }
                        </FormControl>
                    </ListItem>
                </List>
            </CardContent>
        </Card>
    )
}

GaProps.propTypes = {
    props: PropTypes.object,
    dptObjs: PropTypes.object.isRequired,
    onChange: PropTypes.func,
};

export default GaProps;
