import I18n from '@iobroker/adapter-react/i18n';
import crypto from 'crypto';

function extractXml(str) {
    // <ComObject Id="M-0008_A-10D5-11-F6FC-O000A_O-0" FunctionText="Schalten" Name="Taste 1" Number="0" ObjectSize="2 Bytes" Priority="Low" CommunicationFlag="Enabled" ReadFlag="Disabled" ReadOnInitFlag="Disabled" TransmitFlag="Enabled" UpdateFlag="Disabled" WriteFlag="Enabled" Text="Taste 1" />`
    const m = str.match(/(\w+="[^"]*)"/g);
    if (!m) return {};
    const obj = {};
    for (let i = 0; i < m.length; i++) {
        const parts = m[i].split('=');
        obj[parts[0]] = parts[1] ? parts[1].substring(1, parts[1].length - 1) : '';
    }
    return obj;
}

function parse0XML(xml0) {
    const comObjRefs = {};
    const coRxml0 = xml0.match(/<ComObjectInstanceRef\s[^>]+\/>/g);
    if (coRxml0) {
        for (let a = 0; a < coRxml0.length; a++) {
            const ob = extractXml(coRxml0[a]);
            comObjRefs[ob.RefId] = ob.RefId.replace(/_O-.*/, '');
        }
    }
    return comObjRefs;
}

function parseM(str, profiling) {
    let name = str.match(/<ApplicationProgram\s.*Name="([^"]*)"/);
    if (name) {
        name = name[1];
    } else {
        name = null;
    }
    let deviceId = str.match(/<ApplicationProgram\s.*Id="([^"]*)"/);
    if (deviceId) {
        deviceId = deviceId[1];
    } else {
        deviceId = null;
    }
    const comObjectTableM = str.match(/<ComObject\s[^>]+\/>/g);
    const comObjectTable = {};
    if (comObjectTableM) {
        let start1 = Date.now();
        let start;
        profiling && console.log('Extracting ' + comObjectTableM.length);
        for (let c = 0; c < comObjectTableM.length; c++) {
            if (profiling) {
                start = Date.now();
            }
            const ob = extractXml(comObjectTableM[c]);
            comObjectTable[ob.Id] = {
                Text: ob.Text,
                Name: ob.Name,
                Number: ob.Number,
                FunctionText: ob.FunctionText,
                ObjectSize: ob.ObjectSize,
                read: ob.ReadFlag,
                write: ob.WriteFlag,
                transmit: ob.TransmitFlag,
                communicate: ob.CommunicationFlag,
                update: ob.UpdateFlag,
                readOnInit: ob.ReadOnInitFlag,
                dpt: ob.DatapointType
            };
        }
        profiling && console.log(`Extracting2 ${Date.now() - start1}`);
    }

    const comObjectRefsM = str.match(/<ComObjectRef\s[^>]+\/>/g);
    const comObjectRefs = {};
    if (comObjectRefsM) {
        profiling && console.log('Extracting3 ' + comObjectRefsM.length);
        let start1 = Date.now();
        let start;
        for (let cc = 0; cc < comObjectRefsM.length; cc++) {
            const obb = extractXml(comObjectRefsM[cc]);
            comObjectRefs[obb.Id] = {
                Text: obb.Text || '',
                Name: obb.Name || '',
                FunctionText: obb.FunctionText || '',
                RefId: obb.RefId,
                ObjectSize: obb.ObjectSize || null,
                read: obb.ReadFlag || null,
                write: obb.WriteFlag || null,
                transmit: obb.TransmitFlag || null,
                Dpt: obb.DatapointType || null
            };
        }
        profiling && console.log(`Extracting4 ${Date.now() - start1}`);
    }

    return {
        Name: name,//file[key].Name,
        DeviceId: deviceId,//file[key].DeviceId,
        ComObjectTable: comObjectTable,//new dom().parseFromString(file[key].ComObjectTable, 'text/xml'),
        ComObjectRefs: comObjectRefs//new dom().parseFromString(file[key].ComObjectRefs, 'text/xml')
        //  DevParameterBlock: new dom().parseFromString(file[key].DevParameterBlock, 'text/xml')
    };
}

function splitObj(obj) {
    //  console.log(len);
    const splitFile = {};
    for (const a in obj) {
        if (!obj.hasOwnProperty(a)) {
            continue;
        }
        if (typeof obj[a] === 'string') {
            splitFile[a] = obj[a];
        } else {
            const splitPart = obj[a];
            const splitPartLength = Object.keys(splitPart).length;
            if (splitPartLength < 10000) {
                splitFile[a] = splitPart;
            } else {
                let splitSlice = {};
                let pcs = 0;
                let count = 0;
                for (const b in splitPart) {
                    if (!splitPart.hasOwnProperty(b)) {
                        continue;
                    }
                    splitSlice[b] = splitPart[b];
                    if (count < 10000) {
                        count++;
                    } else {
                        splitFile[a + '.' + pcs] = splitSlice;
                        count = 0;
                        pcs++;
                        splitSlice = {};
                    }
                }
                if (count) {
                    splitFile[a + '.' + pcs] = splitSlice;
                }
            }
        }
    }
    return splitFile;
}

function sendObject(socket, instance, obj, callback, _devId) {
    const attrs = Object.keys(obj);
    const pos = attrs.indexOf('DeviceId');
    if (pos !== -1 && pos !== 0) {
        attrs.splice(pos, 1);
        attrs.unshift('DeviceId');
    }
    _devId = _devId || obj.DeviceId;

    for (let b = 0; b < attrs.length; b++) {
        if (obj[attrs[b]] !== null) {
            //    obj[attrs[b]] = null;
            return socket.sendTo('knx.' + instance, 'projectDeviceFile', {file: obj[attrs[b]], prop: attrs[b]})
                .then(result => {
                    if (!result || result.error) {
                        //showMessage(_('Error: ') + (result ? result.error : _('Unknown')));
                    } else {
                        if (result.DeviceId !== _devId) {
                            console.error('Warning object was saved under invalid deviceid ' + result.DeviceId + ' !== ' + _devId);
                        }
                        obj[attrs[b]] = null;
                        setTimeout(() =>
                            sendObject(socket, instance, obj, callback, _devId), 0);
                    }
                });
        }
    }
    callback();
}

function sendFiles(socket, instance, deviceFiles, _resolve) {
    if (!_resolve) {
        return new Promise(resolve => sendFiles(socket, instance, deviceFiles, resolve));
    }
    for (const id in deviceFiles) {
        if (!deviceFiles.hasOwnProperty(id)) {
            continue;
        }
        if (deviceFiles[id]) {
            //
            console.log('Device: ' + id);

            const deviceFilesA = splitObj(deviceFiles[id]);
            deviceFiles[id] = null;

            return sendObject(socket, instance, deviceFilesA, () =>
                setTimeout(() => sendFiles(socket, instance, deviceFiles, _resolve), 0));
        }
    }
    _resolve();
}

async function unzipAsync(file, getPassword) {
    const extractedData = {
        'xml0E': '',
        'knx_masterE': '',
        'projectE': '',
        'devicesFilesE': [],
        'error': ''
    };
    // create a BlobReader to read with a ZipReader the zip from a Blob object
    const reader = new window.zip.ZipReader(new window.zip.BlobReader(file));
    // get all entries from the zip
    const entries = await reader.getEntries();
    let exitValidationFile = false;
    for (let file of entries) {
        //console.log(file.filename);
        if (file.filename === 'knx_master.xml') {
            extractedData['knx_masterE'] = await file.getData(new window.zip.TextWriter())
                .then(res => {
                    const dom = new DOMParser();
                    const doc = dom.parseFromString(res, 'text/xml');
                    const version = doc.getElementsByTagName("KNX")[0].getAttribute("xmlns");
                    if (version === "http://knx.org/xml/project/21") {
                        exitValidationFile = true;
                    }
                    return res;
                });
        }
        if (file.filename === 'project.xml') {
            extractedData['projectE'] = await file.getData(new window.zip.TextWriter())
                .then(res => res);
        }
        if (file.filename.match(/\/0\.xml$/)) {
            extractedData['xml0E'] = await file.getData(new window.zip.TextWriter())
                .then(res => res);
        }
        if (file.filename.match(/\/M.*\.xml$/)) {
            extractedData['devicesFilesE'].push(await file.getData(new window.zip.TextWriter())
                .then(res => {
                    return {filename: file.filename, data: res}
                }));
        }
        if (file.filename.match('.validation')) {
            // this is since ETS6 and so use encoded password in case of protected knxproj
            exitValidationFile = true;
        }

        if (file.filename.endsWith('.zip') && file.filename.startsWith('P-') && !file.filename.startsWith('/')) {
            // request Password
            //console.log(" > Decrypting...");
            await getPassword()
                .then(async password => {
                    const targetBlob = await file.getData(new window.zip.BlobWriter());
                    // create a BlobReader to read with a ZipReader the zip from a Blob object
                    const nestedReader = await new window.zip.ZipReader(await new window.zip.BlobReader(await targetBlob));
                    // get all entries from the zip
                    const nestedFiles = await nestedReader.getEntries();

                    if (exitValidationFile === true) {
                        password = crypto
                            .pbkdf2Sync(
                                Buffer.from(password, 'utf16le'),
                                Buffer.from('21.project.ets.knx.org', 'ascii'),
                                65536,
                                32,
                                'sha256'
                            )
                            .toString('base64');
                    }

                    for (let nestedFile of nestedFiles) {
                        // console.log('decrypted : ' + nestedFile.filename);
                        if (nestedFile.filename.match(/0\.xml$/)) {
                            extractedData['xml0E'] = await nestedFile.getData(new window.zip.TextWriter(), {password: password})
                                .then(res => res)
                                .catch(err => {
                                    extractedData['error'] = I18n.t('wrong Password');
                                });
                        }
                        if (nestedFile.filename.match(/\/M.*\.xml$/)) {
                            extractedData['devicesFilesE'].push(await nestedFile.getData(new window.zip.TextWriter(), {password: password})
                                .then(res => {
                                    extractedData['error'] = I18n.t('wrong Password');
                                })
                                .catch(err => {
                                    return {title: 'importerror', text: I18n.t('Invalid Password'), visible: true};
                                }));
                        }
                        if (nestedFile.filename === 'project.xml') {
                            extractedData['projectE'] = await nestedFile.getData(new window.zip.TextWriter(), {password: password})
                                .then(res => res)
                                .catch(err => {
                                    extractedData['error'] = 'wrong Password';
                                });
                        }
                        if (nestedFile.filename === 'knx_master.xml') {
                            extractedData['knx_masterE'] = await nestedFile.getData(new window.zip.TextWriter(), {password: password})
                                .then(res => res)
                                .catch(err => {
                                    extractedData['error'] = 'wrong Password';
                                });
                        }
                    }
                    await nestedReader.close();
                });
        }
    }

    await reader.close();
    return extractedData;
}

function _extractData(devicesFilesE, deviceFiles, key, cb) {
    if (key < devicesFilesE.length) {
        const start = Date.now();
        const filename = devicesFilesE[key].filename;
        const dom = new DOMParser();
        const doc = dom.parseFromString(devicesFilesE[key].data, 'text/xml');
        const dev = doc.getElementsByTagName('ApplicationProgram')[0].getAttribute('Id');
        const xmlnSpace = doc.getElementsByTagName('KNX')[0].getAttribute('xmlns');

        if (filename.match(/\/M/)) {
            deviceFiles[dev] = parseM(devicesFilesE[key].data, key === 36);
        } else {
            let devComObjectTableTrans = {};
            let devComObjectRefsTrans = {};
            const devComObjectTable = doc.getElementsByTagName('ComObjectTable')[0];
            const devComObjectRefs = doc.getElementsByTagName('ComObjectRefs')[0];
            // const devParameterBlock = doc.getElementsByTagName("Dynamic")[0];

            if (devComObjectTable && typeof devComObjectTable === 'object') {
                devComObjectTableTrans = devComObjectTable.outerHTML;
                //     alert(devComObjectTable.outerHTML);
            } else {
                // devComObjectTableTrans = '<ComObjectTable xmlns="http://knx.org/xml/project/13">\n </ComObjectTable>';
                devComObjectTableTrans = '<ComObjectTable xmlns="' + xmlnSpace + '">\n </ComObjectTable>';
                //    alert(' unklare ComObjectsTables in : ' + dev);
            }

            if (devComObjectRefs && typeof devComObjectRefs === 'object') {
                devComObjectRefsTrans = devComObjectRefs.outerHTML;
            } else {
                //devComObjectRefsTrans = '<ComObjectRefs xmlns="http://knx.org/xml/project/13">\n </ComObjectRefs>';
                devComObjectRefsTrans = `<ComObjectRefs xmlns="${xmlnSpace}">\n </ComObjectRefs>`;
                //    alert(' unklare ComObjectsRefs in : ' + dev);
            }
            deviceFiles[dev] = {
                Name: doc.getElementsByTagName('ApplicationProgram')[0].getAttribute('Name'),
                DeviceId: dev,
                ComObjectTable: devComObjectTableTrans,
                ComObjectRefs: devComObjectRefsTrans
                //       DevParameterBlock: devParameterBlock.outerHTML
            };
        }
        console.log(`Processed ${key} in ${Date.now() - start}ms`);
        setTimeout(() => _extractData(devicesFilesE, deviceFiles, key + 1, cb), 100);
    } else {
        cb(deviceFiles);
    }
}

function extractData(entries) {
    let xml0E = entries['xml0E'] || '';
    let knx_masterE = entries['knx_masterE'] || '';
    const devicesFilesE = entries['devicesFilesE'] || [];
    // let projectE = entries['projectE'] || '';
    const deviceFiles = {};

    if (xml0E.length > 0) {
        parse0XML(xml0E);
    }

    let knx_master;
    if (knx_masterE.length > 0) {
        knx_master = knx_masterE;
    }

    return new Promise(resolve => _extractData(devicesFilesE, deviceFiles, 0, deviceFiles => {
        resolve({xml0: xml0E, deviceFiles, knx_master});
    }));
}

async function fileImport(files, props, options, setProgressState, setImportState, getPassword) {
    const socket = props.socket;
    const instance = props.instance;
    let file = files !== undefined && files[0] ? files[0] : null;
    /*
        const myPingPong = setInterval(pingPong, 1000);

        function pingPong() {
            socket.sendTo('knx.' + instance, 'ping', null)
                .then(res => {
                    console.log(res);
                });
        };
    */

    if (!file) {
        console.warn('invalid file object');
        // 2do Warnmessage
        return false;
    }

    if (file.size > 100000000) {
        console.log('File is too big!');
        // 2do Warnmassage
        return false;
    }
    console.log('file upload start');

    window.zip.useWebWorkers = false;

    let entries;
    try {
        entries = await unzipAsync(file, getPassword);
    } catch (error) {
        setImportState({title: 'importerror', error: error, visible: true});
        return false;
    }

    if (entries['error'].length > 0) {
        setImportState({title: 'importerror', error: entries['error'], visible: true});
    } else {
        const {knx_master, xml0, deviceFiles} = await extractData(entries);

        try {
            console.log('Please wait while generating Objects.....it could take a while.');
            console.warn('socket conntected : ' + socket.connected);
            const res = await socket.sendTo('knx.' + instance, 'options', {'onlyAddNewObjects': options.onlyAddNewObjects});
            console.log(res);

            setProgressState(15);
            let result = await socket.sendTo('knx.' + instance, 'projectStart', {'name': 'test'});
            if (result.error) {
                console.error('Projektstart: ' + (result ? result.error : 'Unknown'));
                throw new Error(result.error);
            }
            setProgressState(33);
            result = await socket.sendTo('knx.' + instance, 'projectXml0', {xml0});
            if (!result || result.error) {
                console.error('projektXml0: ' + result ? result.error : 'Unknown');
                throw new Error(result.error);
            }
            setProgressState(65);
            result = await socket.sendTo('knx.' + instance, 'projectKnxMaster', {knx_master});
            if (!result || result.error) {
                console.error(`projectKnxMaster: ${result ? result.error : 'Unknown'}`);
                throw new Error(result.error);
            }
            setProgressState(90);
            await sendFiles(socket, instance, deviceFiles);
            result = await socket.sendTo('knx.' + instance, 'projectFinished', null);
            console.log(`projectFinished: ${result ? result.error : 'Unknown'}`);
            if (!result || result.error) {
                setImportState({
                    title: 'importerror',
                    error: result.error,
                    numberOfGA: result.count,
                    visible: true
                });
                //  showMessage(_('Error: ') + (result ? result.error : _('Unknown')));
            } else {
                console.error(`Finished ... Extracted: ${result.count}`);

                setProgressState(100);

                setImportState({title: 'importresult', numberOfGA: result.count, visible: true});

                await socket.sendTo('knx.' + instance, 'reload');
            }
        } catch (error) {
            console.error('Cannot parse: ' + error);
            setImportState({title: 'importerror', text: 'error ' + error, visible: true});
        }
    }
}

export default fileImport;
