/*
    GaTools is Base and calls :
        - GaList
        - GaProps
        - GaStateAct
 */

import React, {useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import I18n from '@iobroker/adapter-react/i18n';

import GaStateAct from '../Components/GaStateAct';
import GaList from '../Components/GaList';
import GaProps from '../Components/GaProps';
import {Box, Card, CardContent, Dialog, DialogActions, DialogContent, DialogTitle, Paper} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import IconCancel from "@material-ui/icons/Cancel";
import {makeStyles} from "@material-ui/core/styles";
import GaLinkedState from "../Components/GaLinkedState";

const styles = makeStyles(theme => ({
    root: {
        margin: 0,
        padding: theme.spacing(2)
    },
}))

function createData(gaObj) {
    const ga = Object.entries(gaObj);
    let struct = [];
    let gaRefIdMap = [];
    let gaGaIdMap = [];
    let gaActStateMap = [];
    let gaStateActMap = [];
    let gaUndefMap = [];

    ga.forEach(gt => {
        if (gt[1]) {
            const g = gt[1];
            if (g && g.native && g.native.address) {
                const adr = g.native.address.split('/');
                const nameSplit = gt[0].split('.');
                if (!struct[adr[0]]) {
                    struct[adr[0]] = {hgName: nameSplit[2], hgId: adr[0], mg: []};
                }
                if (struct[adr[0]]) {
                    if (!struct[adr[0]].mg[adr[1]]) {
                        struct[adr[0]].mg[adr[1]] = {mgName: nameSplit[3], mgId: adr[1], ga: []}
                    }
                }
                if (struct[adr[0]] && struct[adr[0]].mg[adr[1]]) {
                    struct[adr[0]].mg[adr[1]].ga[adr[2]] = {gaId: adr[2], gaName: g.common.name, props: {g}}
                    gaRefIdMap[g.native.addressRefId] = g._id;
                    gaGaIdMap[g._id] = g.native.addressRefId;

                    // this is a State
                    if (g.native.actGARefId && g.native.actGARefId.length > 0) {
                        gaStateActMap[g._id] = g.native.actGARefId;
                    }

                    // this is a Act
                    if (g.native.statusGARefId && g.native.statusGARefId.length > 0) {
                        gaActStateMap[g._id] = g.native.statusGARefId;
                    }

                    // this is undef because no dpt
                    if (g.native.dpt && g.native.dpt.length === 0) {
                        gaUndefMap[g._id] = g.native.addressRefId;
                    }

                    // this is undef because no read or write
                    if (!g.common.write || !g.common.read) {
                        gaUndefMap[g._id] = g.native.addressRefId;
                    }

                    // this is undef because neither write nor read are set
                    if (g.common.read && g.common.read.length === 0 && g.common.write && g.common.write.length === 0) {
                        gaUndefMap[g._id] = g.native.addressRefId;
                    }
                }
            }
        }
    });

    return {
        struct: struct,
        gaRefIdMap: gaRefIdMap,
        gaGaIdMap: gaGaIdMap,
        gaActStateMap: gaActStateMap,
        gaStateActMap: gaStateActMap,
        gaUndefMap: gaUndefMap
    }
}

const getGaObject = (socket, adapterName, instance) => {
    return socket.getForeignObjects(adapterName + '.' + instance + '.*')
        .then(res => createData(res));
}

const getDPTObj = (socket, adapterName, instance) => {
    return socket.sendTo(adapterName + '.' + instance, 'getDPT', null);
}

function GaTools(props) {

    const dptTempl = {
        id: ''
    };

    const classes = styles();

    const {socket, adapterName, instance, instanceAlive} = props;

    const [gaObjects, setGaObjects] = useState([]);
    const [gaObj, setGaObj] = useState({});
    const [actObj, setActObj] = useState({});
    const [stateObj, setStateObj] = useState({});
    const [dptObj, setDPTObj] = useState(dptTempl);
    const [refIdMap, setRefIdMap] = useState([]);
    const [gaObjArr, setGaObjArr] = useState([]);
    const [messageText, setMessageText] = useState('');

    //ComponentDidMount=>> bei 1. rendern
    useEffect(() => {
        getGaObject(socket, adapterName, instance)
            .then(gaObj => {
                setGaObjects(gaObj.struct);
                setRefIdMap(gaObj.gaRefIdMap);

// 2Do: Für Multiselect: Änderung von Read und write darf nicht möglich sein wenn gaRefId in actRefId oder StateRefId.
            });
        getDPTs();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [socket]);

    useEffect(() => {
        if (gaObjArr.length === 1) {
            syncGAActState(gaObjArr[0]);
        }
    }, [gaObjArr])

    useEffect(() => {
        // syncGAActState(gaObj);
    }, [gaObj]);


    const handleClick = e => {
        switch (e.id) {
            case 'gaList' :
                if (Array.isArray(e.value)) {
                    let cnt = 0;
                    setGaObjArr([]);
                    let tArr = [];
                    e.value.map(ga => {
                        socket.getObject(ga)
                            .then(obj => {
                                if (obj !== null) {
                                    tArr.push(obj)
                                }
                                if (cnt === e.value.length - 1) {
                                    setGaObjArr(tArr);
                                }
                                cnt++;
                                return
                            })
                        return null;
                    })
                }
                break;
            case 'gaProps' :
                if (e.gaObj) {
                    syncGAActState(e.gaObj);
                }
                if (e.actObj) {

                }
                if (e.stateObj) {

                }
                if (e.gaObjArr) {
                    let deniedObjNameArr = [];
                    for (let _cnt = 0; _cnt < e.gaObjArr.length; _cnt++) {
                        let _tArr = e.gaObjArr[_cnt];
                        if (e.gaObjArr.length > 1 && _tArr.native && _tArr.native.hasOwnProperty('actGARefId') && _tArr.native.actGARefId.length > 0) {
                            deniedObjNameArr.push(_tArr.common.name);
                            //setMessageText(I18n.t('Changing this GA is not possible, because it has a existing Act-State relation. ' + _tArr.common.name));
                        }
                        if (e.gaObjArr.length > 1 && _tArr.native && _tArr.native.hasOwnProperty('statusGARefId') && _tArr.native.statusGARefId.length > 0) {
                            deniedObjNameArr.push(_tArr.common.name);
                            //setMessageText(I18n.t('Changing this GA is not possible, because it has a existing Act-State relation. ' + _tArr.common.name));
                        }
                        if (_tArr.native &&
                            _tArr.native.hasOwnProperty('actGARefId') && _tArr.native.actGARefId.length === 0 &&
                            _tArr.native.hasOwnProperty('statusGARefId') && _tArr.native.statusGARefId.length === 0) {
                            syncGAActState(_tArr);
                        }
                        {
                            deniedObjNameArr.length > 0 &&
                            setMessageText(I18n.t('Changing this GA is not possible, because they have existing Act-State relations.  ') +
                                deniedObjNameArr.map((gaName, index) =>
                                    gaName
                                )
                            )
                        }
                    }
                }
                break;
            case 'gaStateAct' :
                if (e.actId) {
                    socket.getObject(e.actId)
                        .then(res => {
                            // setActObj(res);
                            addGAActState(res);
                        })
                }
                if (e.stateId) {
                    socket.getObject(e.stateId)
                        .then(res => {
                            //setStateObj(res);
                            addGAActState(res);
                        })
                }
                if (e.delete) {
                    deleteGAActState(e.delete);
                }
                break;
            case 'linkedState':
                if (e.linkedState){
                    modifyGALinkedState(e.linkedState);
                }
                break;
            default :
                return null;
        }
    }

    const handlePropsChange = e => {
        if (e.attr && e.value) {
            props.onChange(e.attr, e.value);
        }
    }

    const getDPTs = () => {
        getDPTObj(socket, adapterName, instance)
            .then(res => setDPTObj(res))
    }

    const modifyGALinkedState = obj => {
        socket.setObject(obj._id, obj);
        setGaObjArr([obj]);
    }

    const addGAActState = obj => {
        if (gaObjArr && gaObjArr[0] && gaObjArr[0].native && gaObjArr[0].common && obj && obj.common && obj.native) {
            if (gaObjArr[0].common.hasOwnProperty('read') &&
                gaObjArr[0].common.hasOwnProperty('write') &&
                obj.common.hasOwnProperty('read') &&
                obj.common.hasOwnProperty('write')
            ) {
                // muss geprüft werden auf rw => w oder r=>r oder w=>w wegen update-Flag
                if (((!gaObjArr[0].common.write && gaObjArr[0].common.read && obj.common.write && obj.common.read) ||
                    (gaObjArr[0].common.write && gaObjArr[0].common.read && !obj.common.write && obj.common.read) ||
                    !(gaObjArr[0].common.read === obj.common.read || gaObjArr[0].common.write === obj.common.write) ||
                    (gaObjArr[0].common.write && !gaObjArr[0].common.read && obj.common.write && obj.common.read) ||
                    (gaObjArr[0].common.write && gaObjArr[0].common.read && obj.common.write && !obj.common.read))) {
                    if (gaObjArr[0].native.hasOwnProperty('dpt') &&
                        obj.native.hasOwnProperty('dpt') &&
                        gaObjArr[0].native.dpt === obj.native.dpt
                    ) {
                        if (gaObjArr[0].native.statusGARefId.length === 0 &&
                            gaObjArr[0].native.actGARefId.length === 0 &&
                            obj.native.actGARefId.length === 0 &&
                            obj.native.statusGARefId.length === 0
                        ) {
                            if (gaObjArr[0].native.statusGARefId.length === 0 && obj.common.write && !obj.common.read) {
                                gaObjArr[0].native.actGARefId = obj.native.addressRefId;
                                obj.native.statusGARefId = gaObjArr[0].native.addressRefId;
                            } else if (gaObjArr[0].native.actGARefId.length === 0) {
                                gaObjArr[0].native.statusGARefId = obj.native.addressRefId;
                                obj.native.actGARefId = gaObjArr[0].native.addressRefId;
                            }
                            socket.setObject(gaObjArr[0]._id, gaObjArr[0]);
                            socket.setObject(obj._id, obj);
                            syncGAActState(gaObjArr[0]);
                        } else {
                            setMessageText(I18n.t('The target has already a relation. Delete the target relation first before create a new one. It is connected to ' + gaObjArr[0].common.name));
                        }
                    } else {
                        setMessageText(I18n.t('It is not possible to combine State GA and Act GA with different datapointtypes.'));
                    }
                } else {
                    setMessageText(I18n.t('It is not possible to combine state-state nor act-act GAs. Check the GA Properties.'));
                }
            } else {
                setMessageText(I18n.t('One of State or Act (or both) doesn\'t have defined as read or writeable. Set this property for Act and State GA'));
            }
        }
    }

    const deleteGAActState = objArr => {
        socket.setObject(objArr[0]._id, objArr[0]);
        socket.setObject(objArr[1]._id, objArr[1]);
        if (objArr[0]._id === gaObjArr[0]._id) {
            syncGAActState(objArr[0]);
        } else {
            syncGAActState(objArr[1]);
        }
    }

    /*
    2Do: DPT Abgleich zwischen Act und State
     */


    const updateDPT = (objID, dpt) => {
        let tdpt = dpt.replace('DPT', '');
        socket.getObject(objID)
            .then(res => {
                if (res.hasOwnProperty('native') && res.native.hasOwnProperty('dpt')){
                    if (res.native.dpt !== dpt) {
                        res.common['type'] = dptObj[tdpt].type;
                        res.common['unit'] = dptObj[tdpt].unit;
                        res.common['max'] = dptObj[tdpt].range.max;
                        res.common['min'] = dptObj[tdpt].range.min;
                        res.native['dpt'] = dpt;
                        socket.setObject(objID, res);
                    }
                }
            })
    }

    const syncGAActState = obj => {
        if (obj && obj._id && obj.native && obj.native.hasOwnProperty('actGARefId') && obj.native.hasOwnProperty('statusGARefId') && obj.common.hasOwnProperty('read') && obj.common.hasOwnProperty('write')) {
            if (!obj.common.read && obj.common.write) {
                // this obj is act
                if (obj.native.statusGARefId.length > 0 || obj.native.actGARefId.length > 0) {

                    if (obj.native.actGARefId.length > 0) {
                        // the act Obj can't have a actRefId !
                        obj.native['statusGARefId'] = obj.native.actGARefId;
                        obj.native['actGARefId'] = '';
                        socket.setObject(obj._id, obj);
                    }
                    // check if statusObj is configured properly
                    socket.getObject(refIdMap[obj.native.statusGARefId])
                        .then(statObj => {
                            let modified = false;
                            if (statObj.common && statObj.native) {
                                if (statObj.common['read'] !== true){
                                    statObj.common['read'] = true;
                                    modified = true;
                                }
                                if (statObj.native['actGARefId'] !== obj.native.addressRefId){
                                    statObj.native['actGARefId'] = obj.native.addressRefId;
                                    modified = true;
                                }
                                if (statObj.native['statusGARefId'] !== ''){
                                    statObj.native['statusGARefId'] = '';
                                    modified = true;
                                }
                                if (modified) {
                                    socket.setObject(statObj._id, statObj);

                                }
                            }
                            setActObj(obj);
                            setStateObj(statObj)
                            if (statObj.native && statObj.native.hasOwnProperty('dpt')) {
                                updateDPT(statObj._id, obj.native.dpt);
                            }
                        })
                } else {
                    // in case if stateGA AND actGA are empty
                    socket.setObject(obj._id, obj);
                    setStateObj({});
                }
                if (obj.native && obj.native.hasOwnProperty('dpt')) {
                    updateDPT(obj._id, obj.native.dpt);
                }
                setGaObj(obj);
                setActObj(obj);
            } else if ((obj.common.read && !obj.common.write) || (obj.common.read && obj.common.write)) {
                // this obj is state
                if (obj.native.statusGARefId.length > 0 || obj.native.actGARefId.length > 0) {

                    if (obj.native.statusGARefId.length > 0) {
                        // the state Obj can't have a stateRefId !
                        obj.native['actGARefId'] = obj.native.statusGARefId;
                        obj.native['statusGARefId'] = '';
                        socket.setObject(obj._id, obj);
                    }
                    // check if statusObj is configured properly
                    socket.getObject(refIdMap[obj.native.actGARefId])
                        .then(actObj => {
                            if (actObj.common && actObj.native) {
                                let modified = false;
                                if (actObj.common['read'] !== false){
                                    actObj.common['read'] = false;
                                    modified = true;
                                }
                                if (actObj.common['write'] !== true){
                                    actObj.common['write'] = true;
                                    modified = true;
                                }
                                if (actObj.native['statusGARefId'] !== obj.native.addressRefId){
                                    actObj.native['statusGARefId'] = obj.native.addressRefId;
                                    modified = true;
                                }
                                if (actObj.native['actGARefId'] !== ''){
                                    actObj.native['actGARefId'] = '';
                                    modified = true;
                                }
                                if (modified) {
                                    socket.setObject(actObj._id, actObj);
                                    if (actObj.native && actObj.native.hasOwnProperty('dpt')) {
                                        updateDPT(actObj._id, obj.native.dpt);
                                    }
                                }
                            }
                            setStateObj(obj);
                            setActObj(actObj);
                        })
                } else {
                    // in case if stateGA AND actGA are empty
                    socket.setObject(obj._id, obj);
                    setActObj({});
                }
                setGaObj(obj);
                setStateObj(obj);
            }
            if (obj.native && obj.native.hasOwnProperty('dpt')) {
                updateDPT(obj._id, obj.native.dpt);
            }
        } else {
            setActObj({});
            setStateObj({});
        }
    }

    const showMessage = messageText.length > 0 ?
        <Dialog open={true}>
            <DialogTitle className={classes.root}>
                {I18n.t('Information')}
            </DialogTitle>
            <DialogContent>
                {I18n.t(messageText)}
            </DialogContent>
            <DialogActions>
                <Button variant="contained" onClick={() => setMessageText('')} iconStart={<IconCancel/>}>Cancel</Button>
            </DialogActions>
        </Dialog> :
        null

    return <Box sx={{
        p: 2,
        bgcolor: 'background.default',
        display: 'grid',
        gridTemplateColumns: {md: '1.5fr 2fr 1fr'},
        gap: 2,
        height: '95%',
    }}>
        <Paper style={{maxHeight: '100%', overflow: 'auto'}}>
            <Card sx={{minHeight: '100%'}}>
                <CardContent>
                    <Typography sx={{fontSize: 14}} color="text.secondary" gutterBottom>
                        {I18n.t('GA-structure')}
                    </Typography>
                    <GaList key={'galist'} multiselect={true} gaObjects={gaObjects} onClick={e => handleClick(e)}/>
                </CardContent>
            </Card>
        </Paper>
        <Paper>

        </Paper>
        <Paper>
            {instanceAlive &&
            <GaProps gaObjArr={gaObjArr}
                     dptObjs={dptObj}
                     onClick={e => handleClick(e)}
                     onChange={e => handlePropsChange(e)}
                     props={props}
            />}
            {instanceAlive &&
            <GaStateAct gaObjArr={gaObjArr}
                        actObject={actObj}
                        gaObjects={gaObjects}
                        stateObject={stateObj}
                        gaRefIdMap={refIdMap}
                        socket={socket}
                        onClick={e => handleClick(e)}
            />}
            {instanceAlive &&
                <GaLinkedState
                    socket={socket}
                    gaObjArr={gaObjArr}
                    onClick={e => handleClick(e)}
                />}
        </Paper>
        {showMessage}
    </Box>;
}

GaTools.propTypes = {
    common: PropTypes.object.isRequired,
    native: PropTypes.object.isRequired,
    instance: PropTypes.number.isRequired,
    adapterName: PropTypes.string.isRequired,
    onError: PropTypes.func,
    onLoad: PropTypes.func,
    onChange: PropTypes.func,
    socket: PropTypes.object.isRequired,
    onShowError: PropTypes.func,
    props: PropTypes.object
};

export default GaTools;