import React, {useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import {makeStyles} from '@material-ui/core/styles';
import {
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    IconButton,
    TextField,
    TableContainer,
    Table,
    TableHead,
    TableRow,
    TableCell,
    TableBody,
    Paper,
    Grid,
    CardContent,
    CardActions,
    Card
} from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';
import IconSync from '@material-ui/icons/Refresh';
import IconCheck from '@material-ui/icons/Check';
import IconCancel from '@material-ui/icons/Cancel';
import I18n from '@iobroker/adapter-react/i18n';
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";

const styles = makeStyles(theme => ({
    root: {
        margin: 0,
        padding: theme.spacing(2)
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    dialogWindow: {
        fullWidth: true,
        maxWidth: 'md'
    },
    table: {
        minWidth: '100%',
    },
    title: {
        fontSize: 14,
    }
}));

function AddLicensePopup (props)  {

    const classes = styles();

    const {licensing, socket, instance} = props;

    const [disabledSave, setDisabledSave] = useState(true);
    const [showSyncDialog, setShowSyncDialog] = useState(false);
    const [login, setLogin] = useState('');
    const [password, setPassword] = useState('');
//    const [licenses, setLicenses] = useState(licensing);
    const [validatedLicenses, setValidatedLicenses] = useState([]);
    const [showLicenseChooseDialog, setShowLicenseChooseDialog] = useState(false);
    const [showErrorDialog, setShowErrorDialog] = useState(false);
    const [showErrorDialogMessage, setShowErrorDialogMessage] = useState('');

    const refPassword = React.useRef(); // (1)

    const handleClose = () => {
        props.onChange();
    };

    useEffect(() => {
        showErrorDialogMessage && showErrorDialogMessage.length > 0 ? setShowErrorDialog(true) : setShowErrorDialog(false);
    },[showErrorDialogMessage]);

    const handleChange = (res) => {
        setDisabledSave(false);
        setShowLicenseChooseDialog(false);
        if (Array.isArray(licensing.licenses)) {
            licensing.licenses[0] = res.json ? res.json : res.value;
        }  else {

        }
    }

    const handleSave = (res) => {
        props.onChange(licensing);
    }

    const fetchLicenses = () => {
        setValidatedLicenses([]);

        fetch('https://iobroker.net/api/v1/licenses?type=sw', {headers: {Authorization: `Basic ${btoa(login + ':' + password)}` }})
            .then(async data => await data.json())
            .then(lics => {
                // let tLics = [];
                let t = validatedLicenses;
                const promises = []
                for (let nr = 0; nr < lics.length; nr++) {
                    promises.push(socket.sendTo('knx.' + instance, 'verifyLicense', {license : lics[nr].json})
                        .then(res => {
                            res['json'] = lics[nr].json;
                            t.push(res);
                        }));
                }

                Promise.all(promises)
                    .then(() => {
                        setValidatedLicenses(t);
                        setShowLicenseChooseDialog(true);
                    });

                setShowSyncDialog(false);
            })
            .catch(error => {
                console.log(error.message);
                setShowErrorDialogMessage(error.message);
            });
    };

    const errorDialog = showErrorDialog ?
        <Dialog open={true} fullWidth maxWidth={'md'}>
            <DialogTitle>{I18n.t('Error')}</DialogTitle>
            <DialogContent>
                {I18n.t('This Error occured : ')}{showErrorDialogMessage}
            </DialogContent>
            <DialogActions>
                <Button variant="contained"  onClick={() => setShowErrorDialogMessage('')} iconStart={<IconCancel/>}>{I18n.t('Cancel')}</Button>
            </DialogActions>
        </Dialog>:
        null;

    const licenseChooseDialog = showLicenseChooseDialog ?
        <Dialog open={true} fullWidth maxWidth={'md'}>
            <DialogTitle>{I18n.t('Available Licenses')}</DialogTitle>
            <DialogContent>
                <TableContainer component={Paper}>
                    <Table className={classes.table}  size="medium" aria-label="license table">
                        <TableHead>
                            <TableRow>
                                <TableCell style={{ minWidth: 100 }}>{I18n.t('valid until')}</TableCell>
                                <TableCell align="right">{I18n.t('Datapoints')}</TableCell>
                                <TableCell align="right">{I18n.t('Version')}</TableCell>
                                <TableCell style={{ minWidth: 200 }} align="right">{I18n.t('Host ID')}</TableCell>
                            </TableRow>
                        </TableHead>
                            <TableBody>
                                {Array.isArray(validatedLicenses) && validatedLicenses.length > 0 ?
                                    validatedLicenses.map(lic=>
                                        <TableRow hover key={lic.expiration} onClick={() =>handleChange(lic)}>
                                            <TableCell>{
                                                new Date(lic.expiration * 1000).getDate() + '.' +
                                                new Date(lic.expiration * 1000).getMonth() + '.' +
                                                new Date(lic.expiration * 1000).getFullYear() + '   ' +
                                                new Date(lic.expiration * 1000).getHours() + ':' +
                                                new Date(lic.expiration * 1000).getMinutes() + ':' +
                                                new Date(lic.expiration * 1000).getSeconds()
                                            }</TableCell>
                                            <TableCell align="right">{lic.datapoints}</TableCell>
                                            <TableCell align="right">{lic.majorVersion}</TableCell>
                                            <TableCell align="right" >{lic.sysUUID}</TableCell>
                                        </TableRow>
                                    )
                                    : null
                                }
                            </TableBody>
                    </Table>
                </TableContainer>
            </DialogContent>
            <DialogActions>
                <Button variant="contained" color="primary" onClick={() => fetchLicenses()} iconStart={<IconCheck/>}>{I18n.t('Apply')}</Button>
                <Button variant="contained" onClick={() => setShowLicenseChooseDialog(false)} iconStart={<IconCancel/>}>{I18n.t('Cancel')}</Button>
            </DialogActions>
        </Dialog>:
        null;

    const syncDialog = showSyncDialog ?
        <Dialog open={true}>
            <DialogTitle>{I18n.t('Please enter ioBroker.net login and password')}</DialogTitle>
            <DialogContent>
                <TextField
                    autoFocus
                    fullWidth
                    value={login}
                    onChange={e => setLogin(e.target.value)}
                    label={I18n.t('e-mail')}
                    onKeyUp={e => e.keyCode === 13 && login && refPassword && refPassword.current && refPassword.current.focus()}
                />
                <br/>
                <TextField
                    fullWidth
                    inputRef={refPassword}
                    value={password}
                    onChange={e => setPassword(e.target.value)}
                    type="password"
                    label={I18n.t('password')}
                    onKeyUp={e => e.keyCode === 13 && login && password && fetchLicenses()}
                />

            </DialogContent>
            <DialogActions>
                <Button variant="contained" color="primary" onClick={() => fetchLicenses()} iconStart={<IconCheck/>}>Ok</Button>
                <Button variant="contained" onClick={() => setShowSyncDialog(false)} iconStart={<IconCancel/>}>Cancel</Button>
            </DialogActions>
            {errorDialog}
        </Dialog>
         :
        null;

    return <div>
        <Dialog onClose={handleClose} fullWidth  maxWidth={"md"} aria-labelledby="customized-dialog-title" open={props.visible}>
            <DialogTitle disableTypography className={classes.root}>
                {I18n.t('add / modify license')}
                <IconButton aria-label="close" className={classes.closeButton}  onClick={handleClose}>
                    <CloseIcon />
                </IconButton>
            </DialogTitle>
          <DialogContent dividers>
              <Grid container spacing={1}>

                  {/*licensing && licensing.licenses && Array.isArray(licensing.licenses) && licensing.licenses.length > 1 ?
                      licensing.licenses.map(lic =>
                          <Card>
                              <CardContent>
                                  <Typography className={classes.title} color="textSecondary" gutterBottom>
                                      Word of the Day
                                  </Typography>
                              </CardContent>
                              <CardActions>
                                  <Button variant="contained" autoFocus onClick={handleClose}>
                                      {I18n.t('Close')}
                                  </Button>
                              </CardActions>
                          </Card>
                      )
                      :
                      <Card>
                          <CardContent>
                              <Typography variant="h5" component="h2" gutterBottom>
                                  {I18n.t('Free Licence')}
                              </Typography>
                              <Typography className={classes.pos} color="textSecondary">
                                  {I18n.t('datapoints : ')} max 500
                              </Typography>
                              <Typography className={classes.pos} color="textSecondary">
                                  {I18n.t('valid till : ')} no limit
                              </Typography>
                          </CardContent>
                          <CardActions>

                          </CardActions>
                      </Card>
                  */}

              </Grid>
                <TextField
                    id="standard-multiline-static"
                    fullWidth
                    label={I18n.t('license')}
                    multiline
                    rows={10}
                    defaultValue={licensing && licensing.licenses && Array.isArray(licensing.licenses) ? licensing.licenses[0] : null}
                    variant="standard"
                    onChange={e => {handleChange(e.target)}}
                />
            </DialogContent>
            <DialogActions >
                <Button
                    style= {{flex:1}}
                    variant="contained"
                    onClick={() => setShowSyncDialog(true)}
                    startIcon={<IconSync />}
                >
                    {I18n.t('Synchronize with iobroker.net')}
                </Button>
                <Button variant="contained" color="primary" onClick={handleSave} disabled={disabledSave}>
                    {I18n.t('Save')}
                </Button>
                <Button variant="contained" autoFocus onClick={handleClose}>
                    {I18n.t('Close')}
                </Button>
            </DialogActions>
        </Dialog>
        {syncDialog}
        {licenseChooseDialog}
    </div>;
}

AddLicensePopup.propTypes = {
    socket: PropTypes.object.isRequired,
    instance: PropTypes.number.isRequired,
}

export {AddLicensePopup};
