/*
    GaStateAct is part of
        - GaTools
*/


// 2Do: nur available wenn eine GA ist selected und keine relation hat (state oder act), datenpunkttyp prüfen (number, string,...)

import React, {useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import I18n from '@iobroker/adapter-react/i18n';
import Typography from "@material-ui/core/Typography";
import {
    Card,
    CardActionArea,
    CardActions,
    CardContent,
    Dialog, DialogActions,
    DialogContent,
    DialogTitle,
    Grid,
    TextField
} from "@material-ui/core";
import Button from "@material-ui/core/Button";
import BuildIcon from "@material-ui/icons/Build";
import TreeItem from "@material-ui/lab/TreeItem";
import {makeStyles} from "@material-ui/core/styles";
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import {TreeView} from "@material-ui/lab";
import AppsIcon from "@material-ui/icons/Apps";
import IconCheck from "@material-ui/icons/Check";
import IconCancel from "@material-ui/icons/Cancel";

const styles = makeStyles(theme => ({
    root: {
        margin: 0,
        padding: theme.spacing(2)
    },
}))

TreeItem.propTypes = {
    bgColor: PropTypes.string,
    color: PropTypes.string,
    labelIcon: PropTypes.elementType,
    labelIconColor: PropTypes.string,
    labelInfo: PropTypes.string,
    labelText: PropTypes.string,
};

const useTreeItemStyles = makeStyles((theme) => ({
    root: {
        color: theme.palette.text.secondary,
        '&:hover > $content': {
            backgroundColor: theme.palette.action.hover,
        },
        '&:focus > $content, &$selected > $content': {
            backgroundColor: `var(--tree-view-bg-color, ${theme.palette.grey[400]})`,
            color: 'var(--tree-view-color)',
        },
        '&:focus > $content $label, &:hover > $content $label, &$selected > $content $label': {
            backgroundColor: 'transparent',
        },
    },
    content: {
        color: theme.palette.text.secondary,
        borderTopRightRadius: theme.spacing(2),
        borderBottomRightRadius: theme.spacing(2),
        paddingRight: theme.spacing(1),
        fontWeight: theme.typography.fontWeightMedium,
        '$expanded > &': {
            fontWeight: theme.typography.fontWeightRegular,
        },
    },
    group: {
        marginLeft: 0,
        '& $content': {
            paddingLeft: theme.spacing(2),
        },
    },
    expanded: {},
    selected: {},
    label: {
        fontWeight: 'inherit',
        color: 'inherit',
    },
    labelRoot: {
        display: 'flex',
        alignItems: 'center',
        padding: theme.spacing(0.5, 0),
    },
    labelIcon: {
        marginRight: theme.spacing(1),
    },
    labelIconColor :{
        color: theme.labelIconColor
    },
    labelText: {
        fontWeight: 'inherit',
        flexGrow: 1,
    },
}));

function StyledTreeItem(props) {
    const classes = useTreeItemStyles();
    const { labelText, labelIcon: LabelIcon, labelIconColor, labelInfo, color, bgColor, ...other } = props;

    return (
        <TreeItem
            label={
                <div className={classes.labelRoot}>
                    <AppsIcon style={{ color: labelIconColor }}  />
                    <Typography variant="body2" className={classes.labelText}>
                        {labelText}
                    </Typography>
                    <Typography variant="caption" color="inherit">
                        {labelInfo}
                    </Typography>
                </div>
            }
            style={{
                '--tree-view-color': color,
                '--tree-view-bg-color': bgColor,
            }}
            classes={{
                root: classes.root,
                content: classes.content,
                expanded: classes.expanded,
                selected: classes.selected,
                group: classes.group,
                label: classes.label
            }}
            {...other}
        />
    );
}

function GaLinkedState(props) {

    const {socket, gaObjArr} = props;

    const classes = styles();

    const [iobObjects, setIobObjects] = useState({});
    const [showIobObjectsDialog, setShowIobObjectsDialog] = useState(false);
    const [iobListDialogOKButtonDisabled, setIobListDialogOKButtonDisabled] = useState(true);
    const [lastSelectedItem, setLastSelectedItem] = useState({});
    const [messageText, setMessageText] = useState('');
    const [gaObj, setGaObj] = useState({});

    useEffect(() => {
        getIobObjects();
    },[]);

    useEffect(() => {
        if (Array.isArray(gaObjArr) && gaObjArr.length === 1){
            setGaObj(gaObjArr[0]);
        } else {
            setGaObj({});
        }
    }, [gaObjArr]);

    const handleAddStateClick = () => {
        if (gaObj && gaObj.common && !gaObj.common.read && gaObj.common.write) {
            if (gaObj.native.statusGARefId.length === 0 && gaObj.native.actGARefId.length === 0){
                setShowIobObjectsDialog(true);
            } else {
                setMessageText(I18n.t('This is not allowed if this GA already has a State-Act relation.'));
            }
        } else {
            setMessageText(I18n.t('The source GA has to have only write attribute.'));
        }
    }

    const handleDeleteStateClick = () => {
        if (gaObj && gaObj.native && gaObj.native.linkedState){
            delete gaObj.native.linkedState;
            props.onClick({id: 'linkedState', linkedState: gaObj});
        }
    }

    const handleIobObjectClick = (e, value) => {
        //console.log(e + value);
        if (value._id) {
            setIobListDialogOKButtonDisabled(false);
            setLastSelectedItem(value);
        } else {
            setIobListDialogOKButtonDisabled(true);
            setLastSelectedItem({});
        }
    }

    const handleIobListClick = () => {
        if (lastSelectedItem._id && gaObj && gaObj.native){
            if (lastSelectedItem.common && lastSelectedItem.common.hasOwnProperty('type') &&
            gaObj.common && gaObj.common.hasOwnProperty('type') && gaObj.common.type === lastSelectedItem.common.type){
                // preparing object
                gaObj.common['read'] = false;
                gaObj.common['write'] = true;
                //gaObj.common['type'] = lastSelectedItem.common.hasOwnProperty('type') ? lastSelectedItem.common.type : 'string';
                gaObj.native.actGARefId = '';
                gaObj.native.statusGARefId = '';
                gaObj.native['linkedState'] = lastSelectedItem._id;
                props.onClick({id: 'linkedState', linkedState: gaObj});
                setShowIobObjectsDialog(false);
                setIobListDialogOKButtonDisabled(true);
            } else {
                setMessageText(I18n.t('The value types does not match. Please select a proper Datapointtype for GA.'));
                setShowIobObjectsDialog(false);
                setIobListDialogOKButtonDisabled(true);
            }
        }
    }

    const showMessage = messageText.length > 0 ?
        <Dialog open={true}>
            <DialogTitle className={classes.root}>
                {I18n.t('Information')}
            </DialogTitle>
            <DialogContent>
                {messageText}
            </DialogContent>
            <DialogActions>
                <Button variant="contained" onClick={() => setMessageText('')} iconStart={<IconCancel/>}>Cancel</Button>
            </DialogActions>
        </Dialog> :
        null


    const getIobObjects = () => {
        return socket.getForeignObjects('*')
            .then(res => {
                let tObjObject = {}
                for (let iobObj in res) {
                    if (!(iobObj.match(/^.*knx\./g) ||
                        iobObj.match(/^system\./g) ||
                        iobObj.match(/^admin\./g) ||
                        iobObj.match(/^vis.*\./g) ||
                        iobObj.match(/^web\./g)) ) {
                        let iobObjSplit = iobObj.split('.');

                        if (iobObjSplit.length > 0) {
                            tObjObject[iobObjSplit[0]] = tObjObject[iobObjSplit[0]] ?
                                tObjObject[iobObjSplit[0]] : iobObjSplit.length === 1 ? res[iobObj] : {};
                            if (iobObjSplit.length > 1) {
                                tObjObject[iobObjSplit[0]][iobObjSplit[1]] = tObjObject[iobObjSplit[0]][iobObjSplit[1]] ?
                                    tObjObject[iobObjSplit[0]][iobObjSplit[1]] : iobObjSplit.length === 2 ? res[iobObj] : {};
                                if (iobObjSplit.length > 2) {
                                    tObjObject[iobObjSplit[0]][iobObjSplit[1]][iobObjSplit[2]] = tObjObject[iobObjSplit[0]][iobObjSplit[1]][iobObjSplit[2]] ?
                                        tObjObject[iobObjSplit[0]][iobObjSplit[1]][iobObjSplit[2]] : iobObjSplit.length === 3 ? res[iobObj] : {};
                                    if (iobObjSplit.length > 3) {
                                        tObjObject[iobObjSplit[0]][iobObjSplit[1]][iobObjSplit[2]][iobObjSplit[3]] = tObjObject[iobObjSplit[0]][iobObjSplit[1]][iobObjSplit[2]][iobObjSplit[3]] ?
                                            tObjObject[iobObjSplit[0]][iobObjSplit[1]][iobObjSplit[2]][iobObjSplit[3]] : iobObjSplit.length === 4 ? res[iobObj] : {};
                                        if (iobObjSplit.length > 4) {
                                            tObjObject[iobObjSplit[0]][iobObjSplit[1]][iobObjSplit[2]][iobObjSplit[3]][iobObjSplit[4]] = tObjObject[iobObjSplit[0]][iobObjSplit[1]][iobObjSplit[2]][iobObjSplit[3]][iobObjSplit[4]] ?
                                                tObjObject[iobObjSplit[0]][iobObjSplit[1]][iobObjSplit[2]][iobObjSplit[3]][iobObjSplit[4]] : iobObjSplit.length === 5 ? res[iobObj] : {};
                                            if (iobObjSplit.length > 5) {
                                                tObjObject[iobObjSplit[0]][iobObjSplit[1]][iobObjSplit[2]][iobObjSplit[3]][iobObjSplit[4]][iobObjSplit[5]] = tObjObject[iobObjSplit[0]][iobObjSplit[1]][iobObjSplit[2]][iobObjSplit[3]][iobObjSplit[4]][iobObjSplit[5]] ?
                                                    tObjObject[iobObjSplit[0]][iobObjSplit[1]][iobObjSplit[2]][iobObjSplit[3]][iobObjSplit[4]][iobObjSplit[5]] : iobObjSplit.length === 6 ? res[iobObj] : {};
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                setIobObjects(tObjObject);
            })
    }

    const iobObjectBrowser = showIobObjectsDialog ?
        <Dialog open={true}
                fullWidth={true}
                maxWidth={'md'}>
            <DialogTitle className={classes.root}>
                {I18n.t('Choose')}
            </DialogTitle>
            <DialogContent>
                <TreeView
                    key={'iobobjecttree'}
                    id={'objecttree'}
                    aria-label="ioBroker"
                    defaultExpanded={['3']}
                    defaultCollapseIcon={<ArrowDropDownIcon />}
                    defaultExpandIcon={<ArrowRightIcon />}
                    defaultEndIcon={<div style={{ width: 24 }} />}
                    sx={{ height: '100%', flexGrow: 1, maxWidth: '100%' }}
                    // multiSelect={multiselect ? multiselect : false}
                    onNodeSelect={(e, value) => handleIobObjectClick(e, value)}
                >
                    {iobObjects && !iobObjects.hasOwnProperty('_id') && Object.keys(iobObjects).map(levelOne =>(
                      <StyledTreeItem
                            key={'l1' + levelOne}
                            nodeId={levelOne}
                            labelIcon={AppsIcon}
                            labelIconColor={iobObjects[levelOne].hasOwnProperty('_id') ? 'green' : 'red'}
                            labelText={levelOne}
                        >
                          {iobObjects && iobObjects[levelOne] && !iobObjects[levelOne].hasOwnProperty('_id') && Object.keys(iobObjects[levelOne]).map(levelTwo => (
                              <StyledTreeItem
                                  style={{marginLeft: 20}}
                                  key={'l2' + levelOne + levelTwo}
                                  nodeId={iobObjects[levelOne][levelTwo].hasOwnProperty('_id') ? iobObjects[levelOne][levelTwo] : levelOne + '.' + levelTwo}
                                  labelIcon={AppsIcon}
                                  labelIconColor={iobObjects[levelOne][levelTwo].hasOwnProperty('_id') ? 'green' : 'red'}
                                  labelText={levelTwo}
                              >
                                  {iobObjects && iobObjects[levelOne][levelTwo] && !iobObjects[levelOne][levelTwo].hasOwnProperty('_id') && Object.keys(iobObjects[levelOne][levelTwo]).map(levelThree => (
                                      <StyledTreeItem
                                          style={{marginLeft: 20}}
                                          key={'l3' + levelOne + levelTwo + levelThree}
                                          nodeId={iobObjects[levelOne][levelTwo][levelThree].hasOwnProperty('_id') ? iobObjects[levelOne][levelTwo][levelThree] : levelOne + '.' + levelTwo + '.' + levelThree}
                                          labelIcon={AppsIcon}
                                          labelIconColor={iobObjects[levelOne][levelTwo][levelThree].hasOwnProperty('_id') ? 'green' : 'red'}
                                          labelText={levelThree}
                                      >
                                          {iobObjects && iobObjects[levelOne][levelTwo][levelThree] && !iobObjects[levelOne][levelTwo][levelThree].hasOwnProperty('_id') && Object.keys(iobObjects[levelOne][levelTwo][levelThree]).map(levelFour => (
                                              <StyledTreeItem
                                                  style={{marginLeft: 20}}
                                                  key={'l4' + levelOne + levelTwo + levelThree + levelFour}
                                                  nodeId={iobObjects[levelOne][levelTwo][levelThree][levelFour].hasOwnProperty('_id') ? iobObjects[levelOne][levelTwo][levelThree][levelFour] : levelOne + '.' + levelTwo + '.' + levelThree + '.' + levelFour}
                                                  labelIcon={AppsIcon}
                                                  labelIconColor={iobObjects[levelOne][levelTwo][levelThree][levelFour].hasOwnProperty('_id') ? 'green' : 'red'}
                                                  labelText={levelFour}
                                              >
                                                  {iobObjects && iobObjects[levelOne][levelTwo][levelThree][levelFour] && !iobObjects[levelOne][levelTwo][levelThree][levelFour].hasOwnProperty('_id') && Object.keys(iobObjects[levelOne][levelTwo][levelThree][levelFour]).map(levelFive => (
                                                      <StyledTreeItem
                                                          style={{marginLeft: 20}}
                                                          key={'l5' + levelOne + levelTwo + levelThree + levelFour + levelFive}
                                                          nodeId={iobObjects[levelOne][levelTwo][levelThree][levelFour][levelFive].hasOwnProperty('_id') ? iobObjects[levelOne][levelTwo][levelThree][levelFour][levelFive] : levelOne + '.' + levelTwo + '.' + levelThree + '.' + levelFour + '.' + levelFive}
                                                          labelIcon={AppsIcon}
                                                          labelIconColor={iobObjects[levelOne][levelTwo][levelThree][levelFour][levelFive].hasOwnProperty('_id') ? 'green' : 'red'}
                                                          labelText={levelFive}
                                                      >
                                                          {iobObjects && iobObjects[levelOne][levelTwo][levelThree][levelFour][levelFive] && !iobObjects[levelOne][levelTwo][levelThree][levelFour][levelFive].hasOwnProperty('_id') && Object.keys(iobObjects[levelOne][levelTwo][levelThree][levelFour][levelFive]).map(levelSix => (
                                                              <StyledTreeItem
                                                                  style={{marginLeft: 20}}
                                                                  key={'l6' + levelOne + levelTwo + levelThree + levelFour + levelFive + levelSix}
                                                                  nodeId={iobObjects[levelOne][levelTwo][levelThree][levelFour][levelFive][levelSix].hasOwnProperty('_id') ? iobObjects[levelOne][levelTwo][levelThree][levelFour][levelFive][levelSix] : levelOne + '.' + levelTwo + '.' + levelThree + '.' + levelFour + '.' +levelFive + '.' + levelSix}
                                                                  labelIcon={AppsIcon}
                                                                  labelIconColor={iobObjects[levelOne][levelTwo][levelThree][levelFour][levelFive][levelSix].hasOwnProperty('_id') ? 'green' : 'red'}
                                                                  labelText={levelSix}
                                                              >
                                                              </StyledTreeItem>))
                                                          }
                                                      </StyledTreeItem>))
                                                  }
                                              </StyledTreeItem>))
                                          }
                                      </StyledTreeItem>))
                                  }
                              </StyledTreeItem>))
                          }
                        </StyledTreeItem>
                    ))
                    }
                </TreeView>
            </DialogContent>
            <DialogActions>
                <Button variant="contained" color="primary" disabled={iobListDialogOKButtonDisabled} onClick={() => handleIobListClick()} iconStart={<IconCheck/>}>Ok</Button>
                <Button variant="contained" onClick={() => setShowIobObjectsDialog(false)} iconStart={<IconCancel/>}>Cancel</Button>
            </DialogActions>
        </Dialog>:
        null;

    return (
        <Card sx={{minHeight: '100%'}}>
            <CardContent>
                <Grid container direction={"column"} spacing={2} >
                    <Grid item>
                        <Typography sx={{fontSize: 14}} color="text.secondary" gutterBottom>
                            {I18n.t('Direct Link states')}
                        </Typography>
                    </Grid>
                </Grid>
                <Grid item>
                    <TextField
                        id="standard-read-only-input3"
                        fullWidth
                        label={gaObj && gaObj.native && gaObj.native.hasOwnProperty('linkedState') && gaObj.native.linkedState.length > 0 ? I18n.t('linked with :') : I18n.t('no non-knx ioBroker state linked')}
                        value={gaObj && gaObj.native && gaObj.native.hasOwnProperty('linkedState') && gaObj.native.linkedState.length > 0 ? gaObj.native.linkedState : ''}
                        InputProps={{
                            readOnly: true,
                        }}
                        variant="standard"
                    />
                </Grid>
            </CardContent>
            <CardActionArea>
                <CardActions>
                    {gaObj && gaObj.native &&
                        ((gaObj.native.hasOwnProperty('linkedState') && gaObj.native.linkedState.length === 0) ||
                            !gaObj.native.hasOwnProperty('linkedState')) &&
                    <Button variant="contained"
                             disabled={!gaObj.hasOwnProperty('_id')}
                             onClick={() => handleAddStateClick()}
                             iconStart={<BuildIcon/>}>
                        {I18n.t('add')}
                    </Button>
                    }
                    {gaObj && gaObj.native &&
                    (gaObj.native.hasOwnProperty('linkedState') && gaObj.native.linkedState.length > 0)  &&
                    <Button variant="contained"
                            disabled={!gaObj.hasOwnProperty('_id')}
                            onClick={() => handleDeleteStateClick()}
                            iconStart={<BuildIcon/>}>
                        {I18n.t('delete')}
                    </Button>
                    }
                </CardActions>
            </CardActionArea>
            {iobObjectBrowser}
            {showMessage}
        </Card>
    )
}

GaLinkedState.propTypes = {
    props: PropTypes.object,
    onClick: PropTypes.func
};

export default GaLinkedState;
