/*
    GaStateAct is part of
        - GaTools
*/
import React, {useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import I18n from '@iobroker/adapter-react/i18n';
import GaProps from './GaProps';
import {makeStyles} from '@material-ui/core/styles';
import {
    Card,
    CardActionArea,
    CardActions,
    CardContent,
    DialogActions,
    Dialog,
    DialogContent, DialogTitle, TextField, Grid
} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import IconCancel from "@material-ui/icons/Cancel";
import BuildIcon from '@material-ui/icons/Build';
import GaList from '../Components/GaList';
import IconCheck from "@material-ui/icons/Check";

const styles = makeStyles(theme => ({
    root: {
        margin: 0,
        padding: theme.spacing(2)
    },
}))

function GaStateAct (props) {

    const {gaObjArr, gaRefIdMap, gaObjects, actObject, stateObject, socket} = props;

    const classes = styles();

    const [actObj, setActObj] = useState({});
    const [stateObj, setStateObj] = useState({});
    const [gaObj, setGAObj] = useState({});
    const [showGaListDialog, setShowGaListDialog] = useState(false);
    const [gaListDialogOKButtonDisabled,setGaListDialogOKButtonDisabled] = useState(true);
    const [gaListReturn, setGaListReturn] = useState('');
    const [messageText, setMessageText] = useState('');

    useEffect(() => {
        if (gaObjArr && gaObjArr.length === 1){
            let tGa = gaObjArr[0];
            if (tGa && tGa.common && tGa.native){
                let tGaCommon = tGa.common;
                let tGaNative = tGa.native;
                tGaCommon['read']  = !tGaCommon.hasOwnProperty('read') ? false : tGaCommon.read;
                tGaCommon['write'] = !tGaCommon.hasOwnProperty('write') ? false : tGaCommon.write;
                if ((tGaCommon.read && tGaCommon.write) || (tGaCommon.read && !tGaCommon.write) ) {
                    setActObj({});
                    setStateObj(tGa);
                }
                if(!tGaCommon.read && tGaCommon.write){
                        setActObj(tGa);
                        setStateObj({});
                }
                if (tGaNative.actGARefId && tGaNative.actGARefId.length > 0) {
                    setActObj(socket.getObject(gaRefIdMap[tGaNative.actGARefId]).then(res =>{return res}));
                    setStateObj(tGa);
                }
                if (tGaNative.statusGARefId && tGaNative.statusGARefId.length > 0){
                    setActObj(tGa);
                    setStateObj(socket.getObject(gaRefIdMap[tGaNative.statusGARefId]).then(res =>{return res}));
                }
                if (!tGaCommon.read && !tGaCommon.write ){
                    setActObj({});
                    setStateObj({});
                }
                setGAObj(tGa);
            }
        }
    }, [gaObjArr]);

    useEffect(() => {
        setActObj(actObject);
    }, [actObject]);

    useEffect(() => {
        setStateObj(stateObject);
    }, [stateObject]);

    useEffect(() =>{
        if (gaListReturn && gaListReturn.value && gaListReturn.value.indexOf('knx.') > -1 ) {
            if (gaObj._id !== gaListReturn.value){
                setGaListDialogOKButtonDisabled(false);
            } else {
                setGaListDialogOKButtonDisabled(true);
            }
        } else {
            setGaListDialogOKButtonDisabled(true);
        }
    }, [gaListReturn])

    const handleModifyClick = () => {
        if (gaObj && gaObj.native &&
            (!gaObj.native.hasOwnProperty('linkedState') || (gaObj.native.hasOwnProperty('linkedState') && gaObj.native.linkedState.length === 0))){
            setGaListDialogOKButtonDisabled(true);
            setShowGaListDialog(true);
        } else {
            setMessageText(I18n.t('This is not possible, if this GA is already linked with a non-knx state.'));
        }
    }

    const showMessage = messageText.length > 0 ?
        <Dialog open={true}>
            <DialogTitle className={classes.root}>
                {I18n.t('Information')}
            </DialogTitle>
            <DialogContent>
                {messageText}
            </DialogContent>
            <DialogActions>
                <Button variant="contained" onClick={() => setMessageText('')} iconStart={<IconCancel/>}>Cancel</Button>
            </DialogActions>
        </Dialog> :
        null

    const handleDeleteClick = () => {
        if (actObj && actObj.native && stateObj && stateObj.native){
            // this is a StateObj
            actObj.native.actGARefId = '';
            actObj.native.statusGARefId = '';
            stateObj.native.actGARefId = '';
            stateObj.native.statusGARefId = '';
            setStateObj(stateObj);
            setActObj(actObj);
            props.onClick({id: 'gaStateAct', delete : [actObj, stateObj]});
        }
    }

    const handleGaListClick = () => {
        if (gaObj.common.write && !gaObj.common.read) {
            props.onClick({id: 'gaStateAct', stateId : gaListReturn.value});
            setShowGaListDialog(false);
        } else {
            props.onClick({id: 'gaStateAct', actId : gaListReturn.value});
            setShowGaListDialog(false);
        }
    }

    const getHelperText = ga =>{
        if (ga.length > 0 && ga.match(/\d*\/\d*\/\d*/g)) {
            let hg = ga.split('/')[0];
            let mg = ga.split('/')[1];
            return ga + ' : ' + gaObjects[hg].hgName + ' > ' + gaObjects[hg].mg[mg].mgName;
        } else {
            return '';
        }
    }

    const gaListDialog = showGaListDialog ?
        <Dialog open={true}>
            <DialogTitle className={classes.root}>
                {I18n.t('Choose corresponding GA')}
            </DialogTitle>
            <DialogContent>
                <GaList gaObjects={gaObjects} multiselect={false} onClick={res =>setGaListReturn(res)}/>
            </DialogContent>
            <DialogActions>
                <Button variant="contained" color="primary" disabled={gaListDialogOKButtonDisabled} onClick={() => handleGaListClick()} iconStart={<IconCheck/>}>Ok</Button>
                <Button variant="contained" onClick={() => setShowGaListDialog(false)} iconStart={<IconCancel/>}>Cancel</Button>
            </DialogActions>
        </Dialog>:
        null

    return (
        <Card sx={{ minHeight: '100%'}}>
            <CardContent>
                <Grid container direction={"column"} spacing={2} >
                    <Grid item>
                        <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                            {I18n.t('GA State - Actuator relation')}
                        </Typography >
                    </Grid>
                    <Grid item>
                        <TextField
                            id="standard-read-only-input1"
                            fullWidth
                            label="Actuator GA"
                            helperText={getHelperText(gaObjArr.length === 1 && actObj && actObj.native && actObj.native.hasOwnProperty('address') ? actObj.native.address : '')}
                            value={gaObjArr.length === 1 && actObj && actObj.common && actObj.common.hasOwnProperty('name') ? actObj.common.name : '' }
                            InputProps={{
                                readOnly: true,
                            }}
                            variant="standard"
                        />
                    </Grid>
                    <Grid item>
                        <TextField
                            id="standard-read-only-input2"
                            fullWidth
                            label="State GA"
                            helperText={getHelperText(gaObjArr.length === 1 && stateObj && stateObj.native && stateObj.native.hasOwnProperty('address')  ? stateObj.native.address : '')}
                            value={gaObjArr.length === 1 && stateObj && stateObj.common && stateObj.common.hasOwnProperty('name')  ? stateObj.common.name : ''}
                            InputProps={{
                                readOnly: true,
                            }}
                            variant="standard"
                        />
                    </Grid>
                </Grid>
        </CardContent>
            <CardActionArea>
                <CardActions>
                    {gaObjArr.length === 1 && (actObj.hasOwnProperty('_id')  && stateObj.hasOwnProperty('_id')) &&
                    <Button variant="contained"
                            onClick={() => handleDeleteClick()}
                            iconStart={<BuildIcon/>}>
                        {I18n.t('delete')}
                    </Button>
                    }
                    {gaObjArr.length === 1 && ((!actObj.hasOwnProperty('_id')  && stateObj.hasOwnProperty('_id')) || ((actObj.hasOwnProperty('_id')  && !stateObj.hasOwnProperty('_id')))) &&
                    <Button variant="contained"
                            key={'newRelation'}
                            onClick={e=>handleModifyClick(e)}
                            iconStart={<BuildIcon/>}
                    >
                        {I18n.t('add new relation')}
                    </Button>
                    }
                </CardActions>
            </CardActionArea>
            {gaListDialog}
            {showMessage}
    </Card>);
}

GaProps.propTypes = {
    props: PropTypes.object.isRequired,
    gaObjArray : PropTypes.array,
    actObject : PropTypes.object,
    gaObjects : PropTypes.array.isRequired,
    stateObject : PropTypes.object,
    onClick: PropTypes.func
};

export default GaStateAct;