import React, {useEffect, useState} from 'react';
import {makeStyles, MuiThemeProvider} from '@material-ui/core/styles';
import PropTypes from 'prop-types';

import fileImport from '../Tools/Projectupload';

import I18n from '@iobroker/adapter-react/i18n';
import Logo from '@iobroker/adapter-react/Components/Logo';

import {AddLicensePopup} from '../Components/LicensePopupWindow';
import {StyledDropZone} from '../Components/StyledDropZone';
import {
    Box,
    Card,
    CardContent,
    Checkbox,
    Dialog,
    FormControlLabel,
    Grid, InputLabel,
    LinearProgress, Select,
    TextField,
    DialogActions,
    DialogContent,
    DialogTitle,
    FormControl,
    MenuItem
} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import {Alert} from "@material-ui/lab";
import Button from "@material-ui/core/Button";

const styles = makeStyles(theme => ({
    textField: {
        '& > *': {
            margin: theme.spacing(1),
            width: '27ch',
        },
        margin: theme.spacing(1),
    },
    input: {
        '& > *': {
            margin: theme.spacing(1),
            width: '25ch',
        },
    },
    alertBar: {
        '& > *': {
            margin: theme.spacing(1),
            width: '100%',
        },
    },
    progress: {
        height: 20
    },
    root: {
        height: 10,
        borderRadius: 0,
    },
    fullWidth: {
        width: '98%'
    },
    tab: {
        marginRight: '2rem',
        marginLeft: '0.5rem'
    },
    card: {
        backgroundColor: theme.palette.type === 'dark' ? '#111' : '#EEE'
    },
    controlElement: {}
}));

function Configuration(props) {
    const classes = styles();

    const {instanceAlive} = props;
    const [passwordDialogOKButton, setPasswordDialogOKButton] = useState(true);
    const [isInstanceAlive, setIsInstanceAlive] = useState( false);
    const [sysUUID, setSysUUID] = useState('');
    const [numberOfDPTs, setNumberOfDPTs] = useState(0);
    const [progressState, setProgressState] = useState(0);
    const [ipList, setIpList] = useState([]);
    const [options, setOptions] = useState({onlyAddNewObjects: true});
    const [licenseVerification, setLicenseVerification] = useState({
        name: '',
        expiration: 0,
        offlineLicense: false,
        numberOfInstances: '',
        majorVersion: '',
        registeredTo: '',
        message: '',
        datapoints: 0,
        sysUUID: '',
        json: ''
    });

    const [passwordDialogVisible, setPasswordDialogVisible] = useState(false);

    const [showPopup, setShowPopup] = useState({
        addLicensePopupVisible: false,
        disableSaveButton: true
    });

    const [licensing, setLicensing] = useState({
        licenses: [props.native.license || ''],
        valid: false,
        permissions: '',
        error: ''
    });
    const [importState, setImportState] = useState({
        visible: false,
        finished: false,
        title: 'title',
        text: 'test',
        numberOfDevices: 0,
        numberOfGA: 0,
        devices: [],
        licenses: [props.native.license || ''],
        attr: ''
    });


    //ComponentDidMount=>> bei 1. rendern
    useEffect(() => {
        props.socket.getUuid()
            .then(uuid => {
                setSysUUID(uuid);
                return props.socket.getForeignObjects(props.adapterName + '.' + props.instance + '.*')
                    .then(res => {
                        // object mit prop 'connection' darf nicht mitgezählt werden
                        if (res.hasOwnProperty('knx.0.info.connection')) {
                            delete res['knx.0.info.connection'];
                        }
                        const ga = Object.entries(res);
                        setNumberOfDPTs(ga.length);
                        return props.socket.getHostByIp(props.common.host)
                            .then(ips => {
                                // [{name, address, family}]
                                ips = ips.filter(item => item.family === 'ipv4' && item.address !== '0.0.0.0');
                                setIpList(ips);
                            });

                    });
            });
        // Aufräumfunktion
        //  return () => props.socket.unsubscribeState(`system.adapter.${props.adapterName}.${props.instance}.alive`, onAliveChanged);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []); // <==Bedingung)

    useEffect(() => {
        checkLicense(props.native.license);
        //setIsInstanceAlive(props.instanceAlive);

        // verifyLicense(props.native.license);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [sysUUID, numberOfDPTs, props.instanceAlive]);

    useEffect(() =>{
        if (instanceAlive !== undefined) {
            if (instanceAlive && instanceAlive.hasOwnProperty('val')){
                setIsInstanceAlive(instanceAlive.val);
            } else
                if (instanceAlive || !instanceAlive){
                    setIsInstanceAlive(instanceAlive || false);
                }
        }
    }, [props.instanceAlive]);

    useEffect(() => {
        props.onChange('validLicense', licensing.valid);
    }, [licensing.valid]);

    /*
    useEffect(() => {
        console.log('configuration: socket connected : ' + props.socket.connected);
    }, [props.socket.connected]);
*/
    const verifyLicense = license => {
        props.socket.sendTo('knx.' + props.instance, 'verifyLicense', {license})
            .then(res =>
                setLicenseVerification(res)
            );
    };

    const checkPassword = pass => {
        if (pass === undefined || pass.length < 8 || !pass.match(/[a-z]/) || !pass.match(/[A-Z]/) || !pass.match(/\d/)) {
            return I18n.t('invalid password');
        } else {
            return false;
        }
    }

    const checkIpAddress = ipAdr => {
        const ipFormat = /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
        if (ipAdr === undefined || !ipAdr.match(ipFormat)) {
            return I18n.t('invalid ip address');
        } else {
            return false;
        }
    }

    const checkEibAddress = eibAdr => {
        const eibFormat = /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
        if (eibAdr === undefined || !eibAdr.match(eibFormat)) {
            return I18n.t('invalid KNX address');
        } else {
            return false;
        }
    }

    const checkPort = port => {
        const portNr = parseInt(port, 10);
        if (portNr === undefined || isNaN(port) || !Number.isInteger(portNr) || portNr < 1025 || portNr > 65536) {
            return I18n.t('invalid_Port_warning');
        } else {
            return false;
        }
    }

    const checkFrameInterval = frameInterval => {
        const frameInt = parseInt(frameInterval, 10);
        if (frameInt === undefined || isNaN(frameInterval) || !Number.isInteger(frameInt) || frameInt < 10 || frameInt > 300) {
            return I18n.t('invalid frame interval');
        } else {
            return false;
        }
    }

    const fileCallback = async fromDropzone => {
        await fileImport(fromDropzone, props, options, setProgressState, setImportState, getPassword);
    };

    const checkLicense = license => {
       // if (license) {
        license = license === undefined ? "" : license;
        props.socket.sendTo('knx.' + props.instance, 'verifyLicense', {license})
                .then(res => {
                    if (license === ""){
                        setLicensing({
                            valid: true,
                            error: '',
                            licenses: [license]
                        });
                    } else if (res.majorVersion === '<2') {
                        setLicensing({
                            valid: false,
                            error: 'This license is for adapter versions <2',
                            licenses: [license]
                        });
                    } else if ((res.expires * 1000 > new Date().getTime())) {
                        setLicensing({
                            valid: false,
                            error: 'Cannot check license: Expired on ' + new Date(res.expires * 1000).toString(),
                            licenses: [license]
                        });
                    } else if (res.sysUUID !== sysUUID) {
                        setLicensing({
                            valid: false,
                            error: 'This license is not valid for this iobroker host',
                            licenses: [license]
                        });
                    } else {
                        setLicensing({valid: true, error: '', licenses: [license]});
                    }
                    setLicenseVerification(res);
                });
        //} else {
        //    setLicensing({valid: false, error: 'not installed', licenses: ['']});
       // }
    };

    const renderInput = (title, attr, type) => {
        let error;
        switch (attr) {
            case 'pass' :
                error = checkPassword(props.native[attr]);
                break;
            case 'gwip' :
                error = checkIpAddress(props.native[attr]);
                break;
            case 'eibadr' :
                error = checkEibAddress(props.native[attr]);
                break;
            case 'gwipport' :
                error = checkPort(props.native[attr]);
                break;
            case 'frameInterval' :
                error = checkFrameInterval(props.native[attr]);
                break;
            case 'bind' :
                error = checkIpAddress(props.native[attr]);
                break;
            default :
                error = false;
        }

        return <TextField
            label={I18n.t(title)}
            error={!!error}
            className={props.classes && props.classes.input ? props.classes.input : classes.input}
            value={props.native[attr]}
            type={type || 'text'}
            helperText={error || ''}
            onChange={e => props.onChange(attr, e.target.value)}
        />;
    };

    const renderCheckbox = (title, attr) => {
        return <FormControlLabel
            key={attr}
            className={props.classes && props.classes.controlElement ? props.classes.controlElement : classes.controlElement}
            control={
                <Checkbox
                    checked={options[attr]}
                    onChange={(event) => setOptions(old => ({
                        ...old,
                        [attr]: event.target.checked
                    }))}
                    color="primary"
                />
            }
            label={I18n.t(title)}
        />;
    };

    const handleShowImportStateClose = () => {
        setImportState({visible: false});
        setNumberOfDPTs(importState.numberOfGA);
    }

    function getPassword() {
        return new Promise((resolve, reject) => {
            setPasswordDialogVisible(true);
            let okBtn = document.getElementById('dialog-pw-ok-btn');
            okBtn.addEventListener('click', (evt) => {
                let pw = document.getElementById('standard-password-input');
                if (pw && pw.value){
                    setPasswordDialogVisible(false);
                    resolve(pw.value);
                }
            });
        });
    }

    const showPasswordDialog = passwordDialogVisible  ?
        <Dialog open={true}>
            <DialogTitle>
                {I18n.t('Password for knx project file')}
            </DialogTitle>
            <DialogContent>
                <TextField
                    id="standard-password-input"
                    label="Password"
                    type="password"
                    autoComplete="current-password"
                    variant="standard"
                    onChange={(e) => { setPasswordDialogOKButton(e.target.value.length > 0 ? false : true)}}
                />
            </DialogContent>
            <DialogActions>
                <Button variant="contained" id="dialog-pw-ok-btn" disabled={passwordDialogOKButton} autoFocus >
                    {I18n.t('OK')}
                </Button>
                <Button variant="contained" autoFocus onClick={() => { setPasswordDialogVisible(false); setPasswordDialogOKButton(false)}}>
                    {I18n.t('Close')}
                </Button>
            </DialogActions>
        </Dialog> :
        null;

    const showImportState = importState.visible ?
        <Dialog open={true}>
            <DialogTitle>
                {I18n.t('Importstate')}
            </DialogTitle>
            <DialogContent>
                {importState.title === 'importresult' ?
                    I18n.t('Successful imported GAs : ') + importState.numberOfGA :
                    importState.title === 'importerror' ?
                        I18n.t('Import partially or completely failed with error : ') + I18n.t(importState.error) : ''
                }
            </DialogContent>
            <DialogActions>
                <Button variant="contained" autoFocus onClick={() => handleShowImportStateClose()}>
                    {I18n.t('Close')}
                </Button>
            </DialogActions>
        </Dialog> :
        null;

    const addLicenseOnChange = (res) => {
        setShowPopup({addLicensePopupVisible: false});
        if (res && res.licenses && Array.isArray(res.licenses)) {
            setLicensing(res.licenses[0]);
            checkLicense(res.licenses[0]);
            verifyLicense(res.licenses[0]);
            /*
                        props.socket.getObject('system.adapter.' + props.adapterName + '.' + props.instance)
                            .then(obj => {
                                if (obj.native && obj.native.license){
                                    obj.native.license = res.licenses[0] || '';
                                    props.socket.setObject('system.adapter.' + props.adapterName+ '.' + props.instance, obj);
                                }
                        })
                        */
            props.onChange('license', res.licenses[0] || '');
        }
    };

    const renderLicenseFeatures = licVerification => <div>
        <Alert severity={licVerification.datapoints >= numberOfDPTs ? 'success' : 'error'}>
            {I18n.t('number of max datapoints : ')}{numberOfDPTs || 0} / {licVerification.datapoints || 0}
        </Alert>
        <Alert severity={licVerification.expiration * 1000 > new Date().getTime() ? 'success' : 'error'}>
            {I18n.t('valid until : ')} {
            licVerification.expiration * 1000 < new Date().getTime() ?
                new Date(licVerification.expiration * 1000).toString() || I18n.t('expired') :
                new Date(licVerification.expiration * 1000).toString()
        }
        </Alert>

        <Alert severity={'success'}>
            {I18n.t('version : ')} {licVerification.majorVersion}
        </Alert>
    </div>;

    return <MuiThemeProvider theme={props.theme}>
        <form className={classes.tab}>
            <div>
                <Logo
                    instance={props.instance}
                    common={props.common}
                    native={props.native}
                    onError={text => this.setState({errorText: text})}
                    onLoad={props.onLoad}
                    classes={{}}
                    className=""
                />
            </div>

            <Grid container spacing={1} justifyContent="center">
                <Grid item xs={7}>
                    <Card classes={{root: props.classes && props.classes.card ? props.classes.card : classes.card}}>
                        <CardContent>
                            <Typography gutterBottom variant="h5" component="h2">
                                {I18n.t('KNX Gateway Settings')}
                            </Typography>
                            <span style={{marginright: '0rem'}}>{renderInput('KNX Gateway IP ', 'gwip', 'ipAddress')}</span>
                            <span style={{ marginright: '1.5rem', marginLeft: '0rem' }}>{renderInput('KNX Gateway Port ', 'gwipport', 'port')}</span>
                            <span style={{ marginright: '1.5rem',marginLeft: '0rem' }}>{renderInput('physical KNX address', 'eibadr', 'eibAddress')}</span>
                            <span style={{ marginLeft: '0rem' }}>{renderInput('KNX packages per second', 'frameInterval', 'frameInterval')}</span>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item xs={3}>
                    <Card className={props.classes && props.classes.card ? props.classes.card : classes.card}>
                        <CardContent>
                            <Typography gutterBottom variant="h5" component="h2">
                                {I18n.t('local iobroker IP')}
                            </Typography>
                            <FormControl
                                className={props.classes && props.classes.input ? props.classes.input : classes.input}
                            >
                                <InputLabel>{I18n.t('local iobroker IP')}</InputLabel>
                                <Select
                                    value={props.native.bind}
                                    style={{width: '100%'}}
                                    onChange={e => props.onChange('bind', e.target.value)}
                                >
                                    {ipList.map(item => <MenuItem key={item.address}
                                                                  value={item.address}>{item.name}</MenuItem>)}
                                </Select>
                            </FormControl>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item xs={2}>
                    <Card className={props.classes && props.classes.card ? props.classes.card : classes.card}>
                        <CardContent>
                            <Typography gutterBottom variant="h5" component="h2">
                                {I18n.t('loglevel')}
                            </Typography>
                            <FormControl
                                className={props.classes && props.classes.input ? props.classes.input : classes.input}>
                                <InputLabel>{I18n.t('loglevel')}</InputLabel>
                                <Select
                                    value={props.native.debugLevel}
                                    onChange={e => props.onChange('debugLevel', e.target.value)}
                                >
                                    <MenuItem value={0}>{I18n.t('Info')}</MenuItem>
                                    <MenuItem value={2}>{I18n.t('more')}</MenuItem>
                                    <MenuItem value={3}>{I18n.t('all')}</MenuItem>
                                </Select>
                            </FormControl>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item xs={12}>
                    <Card className={props.classes && props.classes.card ? props.classes.card : classes.card}>
                        <CardContent>
                            <Typography gutterBottom variant="h5" component="h2">
                                {I18n.t('upload KNX project file')}
                            </Typography>
                            {isInstanceAlive && licensing.valid ?
                                <div>
                                    <div>{renderCheckbox('only import new datapoints', 'onlyAddNewObjects')}</div>
                                    <div style={{marginTop: '2rem'}}>
                                        <StyledDropZone onDrop={fileCallback}/>
                                    </div>
                                    <Box sx={{width: '100%'}} id={'status'} style={{marginTop: '2rem'}}>
                                        <LinearProgress variant="determinate" value={progressState}
                                                        className={classes.progress}/>
                                    </Box>
                                </div> :
                                <Alert severity={'error'}>
                                    {isInstanceAlive ?
                                        I18n.t('license not valid'):
                                        I18n.t('adapter not running')

                                    }
                                </Alert>
                            }
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item xs={12}>
                    <Card className={props.classes && props.classes.card ? props.classes.card : classes.card}>
                        <CardContent>
                            {isInstanceAlive ?
                                <div>
                                    <Typography gutterBottom variant="h5" component="h2">
                                        {I18n.t('KNX adapter licensing')}
                                    </Typography>
                                    {I18n.t('Host ID (Serial-Number(UUID)) : ')} {props.hostUUID}
                                    <p></p>
                                    <Alert
                                        onClick={() => setShowPopup({addLicensePopupVisible: true}) /*setShowLicAlert(!showLicAlert)*/}
                                        severity={licensing.valid ? 'success' : 'error'}
                                    >
                                        {licensing.valid ?
                                            I18n.t('License valid.') :
                                            licensing.error && licensing.error.message === 'Failed to fetch' ?
                                                I18n.t('License server not available.') :
                                                I18n.t('License error : ') + licensing.error + I18n.t('. Click here to install a license.')
                                        }
                                        {licensing.valid ? renderLicenseFeatures(licenseVerification) : null}
                                    </Alert>
                                </div> :
                                <Alert severity={'error'}>
                                    {I18n.t('adapter not running')}
                                </Alert>
                            }
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
            {showImportState}
            {showPasswordDialog}
            {showPopup.addLicensePopupVisible &&
            <AddLicensePopup
                instance={props.instance}
                socket={props.socket}
                props={props}
                licensing={licensing}
                onChange={e => addLicenseOnChange(e)}
                visible={showPopup.addLicensePopupVisible}/>
            }
        </form>
    </MuiThemeProvider>;
}

Configuration.propTypes = {
    common: PropTypes.object.isRequired,
    native: PropTypes.object.isRequired,
    instance: PropTypes.number.isRequired,
    adapterName: PropTypes.string.isRequired,
    onError: PropTypes.func,
    onLoad: PropTypes.func,
    onChange: PropTypes.func,
    changed: PropTypes.bool,
    socket: PropTypes.object.isRequired,
    progressState: PropTypes.number,
    props: PropTypes.object,
    instanceAlive: PropTypes.bool.isRequired
};

export default Configuration;
